// 引入 Three.js 库
import * as THREE from "three";

export default class SkyBoxs {
  constructor(scene) {
    this.scene = scene;
  }

  /**
   * 添加雾效果
   * @param color 雾的颜色，默认为灰色
   * @param near 雾的起始距离，默认为500
   * @param far 雾的结束距离，默认为2000
   */
  addFog(color = 0xa0a0a0, near = 500, far = 2000) {
    this.scene.fog = new THREE.Fog(new THREE.Color(color), near, far);
  }

  /**
   * 移除雾效果
   */
  removeFog() {
    this.scene.fog = null;
  }

  /**
   * 天空盒类型
   * @param SkyType 天空类型
   */
  addSkybox(SkyType) {
    const path = `/static/skybox/${SkyType}/`;
    this.setSkybox(path);
  }

  /**
   * 自定义添加天空盒
   * @param path 天空盒地址
   * @param format 图片后缀名，默认为'.jpg'
   */
  setSkybox(path, format = ".jpg") {
    const loaderbox = new THREE.CubeTextureLoader();
    const cubeTexture = loaderbox.load(this.getSkyboxUrls(path, format));
    cubeTexture.encoding = THREE.sRGBEncoding;
    this.scene.background = cubeTexture;
  }

  /**
   * 生成天空盒的完整URL数组
   * @param path 天空盒路径
   * @param format 图片后缀名
   * @returns 完整的天空盒URL数组
   */
  getSkyboxUrls(path, format) {
    return [
      `${path}posx${format}`,
      `${path}negx${format}`,
      `${path}posy${format}`,
      `${path}negy${format}`,
      `${path}posz${format}`,
      `${path}negz${format}`,
    ];
  }
}



