<template>
  <!-- 刻度尺 -->
  <div class="box">
    <div class="container" :style="{ width: (ruleData.length-1) * 150+2 + 'px' }">
      <span
        v-for="(item, index) in ruleData"
        :key="item"
        :style="{ left: index * 145 - 10 + 'px' }"
      >
        {{ item }}
      </span>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    curData: {
      type: Number,
      require: true,
      default: 0,
    },
    ruleData: {
      type: Array,
      require: true,
      default: () => ["0", "1天", "2", "3", "4", "5", "6", "9天"],
    },
  },
  data() {
    return {};
  },
  computed: {
    curWidth() {
      return (this.curData / this.ruleData.length) * 99;
    },
  },
  methods: {
    activeStatus(i) {
      return this.curData == i;
    },
    setStatus(i) {
      this.$emit("update:curData", i);
    },
  },
};
</script>
<style lang="scss" scoped>
.container {
  transform: rotateX(180deg);
  position: relative;
  left: 10px;
  top: 0;
  height: 50px;
  display: block;
  background-repeat-y: no-repeat;
  background-image: linear-gradient(270deg, #666 2px, transparent 0),
    linear-gradient(270deg, #999 2px, transparent 0);
  background-size: 150px 20px, 15px 10px;
}
.box {
  position: relative;
  width: 500px;
  overflow-y: hidden;
  overflow-x: scroll;
  height: 60px;
  border-top: 1px solid pink;
}
.box::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom right, #ccc 0%, #ccc 100%);
  border-radius: 5px;
}

.box::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

span {
  transform: rotateX(180deg);
  position: absolute;
  font-size: 14px;
  color: #999;
  top: 20px;
}
</style>


