import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)


export const baseRoutes = [
  {
    path: '/',
    name: 'home',
    title:"首页",
    icon:'el-icon-star-off',
    component: ()=>import('../Home')
  },
  {
    path: '/fullPage',
    name: 'fullPage',
    title:"fullPage",
    icon:'el-icon-star-off',
    component: ()=>import('@/fullPage/scalebox/index.vue')
  },
]



export function createRouter() {
  return new Router({
    scrollBehavior: () => ({ y: 0 }),
    routes: [...baseRoutes],
  })
}
const router = createRouter()

export function resetRoutes() {
  const newRouter = createRouter()
  router.options.routes.splice(0, router.options.routes.length)
  router.matcher = newRouter.matcher
}

export function setRoutes(routes) {
  resetRouter()
  router.addRoutes(routes)
  Array.prototype.push.apply(router.options.routes, routes)
}



export const getLocalRoutes = function (router) {
  resetRoutes()
  const reqs = require.context("@/modules", true, /route\.js/)
  let analysis = function (req, p) {
    let reg = new RegExp('^' + p.replace('/route.js', '') + '\/[a-zA-Z]+\/route.js$')
    reqs.keys().forEach((dpath) => {
      if (reg.test(dpath)) {
        let cReq = require(`@/modules/${dpath.replace('./', '')}`).default
        if (req.children) {
          req.children.push(cReq)
        } else {
          req.children = [cReq]
        }
        analysis(cReq, dpath)
      }
    })
  }
  let ccc=[]
  reqs.keys().forEach((path) => {
    if (/^\.\/[a-zA-Z]+\/route\.js$/.test(path)) {
      let req = require(`@/modules/${path.replace('./', '')}`).default
      // console.log(req,path)
      analysis(req, path)
      if (path) {
       ccc.unshift(req)
      } else {
        ccc.push(req)
      }
    }
  })
  
  baseRoutes[0].children=ccc
  // console.log(ccc)
  router.options.routes.unshift(...baseRoutes)
  // console.log('11111111',router.options.routes)
  router.addRoutes(router.options.routes)
}


export default router





