<template>
  <div>
    <div
      ref="model"
      style="
        width: calc(100vw -600px);
        height: 75vh;
        margin: 30px;
        border: 1px solid pink;
        position: relative;
      "
    ></div>
  </div>
</template>

<script>
import renderModel from "./js/renderModel";
import * as THREE from "three";
import { createFlame } from "./js/flame";

export default {
  components: {},
  mounted() {
    this.init();
  },
  data() {
    return {};
  },
  watch: {},
  methods: {
    init() {
      let config = {
        camera: {
          fov: 45,
          near: 1,
          far: 100000,
          position: [600, 750, -1221],
          rotation: [0, 0, 0],
        },
      };
      this.modelApi = new renderModel(this.$refs.model, config);

      // 使用加载模型的函数
      this.modelApi
        .loadModel("/static/FBX/shanghai.FBX")
        .then((object) => {
          console.log("模型加载成功", object);
          object.traverse((child) => {
            console.log(child.name);
            if (child.isMesh && child.name === "CITY_UNTRIANGULATED") {
              //CITY_UNTRIANGULATED  ROADS LANDMASS
              // 检查子对象是否为 Mesh
              const { geometry } = child; // 使用子对象的几何体
              geometry.computeBoundingBox();
              geometry.computeBoundingSphere();
              const { max, min } = geometry.boundingBox;

              const shaderMaterial = new THREE.ShaderMaterial({
                depthWrite: true,
                depthTest: true,
                transparent: true, //如果材质透明，那么楼宇就被渲染到后面了
                side: THREE.DoubleSide, //双面渲染
                uniforms: {
                  uColor: { value: new THREE.Color("#e523ff") }, // 默认颜色红色
                  uTopColor: { value: new THREE.Color("#ffff00") }, // 顶部颜色黄色
                  uOpacity: { value: 0.5 }, // 透明度
                  uMin: { value: min }, // 绑定最小坐标
                  uMax: { value: max }, // 绑定最大坐标
                  uBorderWidth: { value: 5 }, // 边界宽度
                  uLightColor: { value: new THREE.Color("#ffffff") }, // 光线颜色
                  uCircleTime: { value: 5 }, // 圆周时间
                  uTime: { value: 1.0 }, // 时间
                  uGradient: { value: true }, // 是否启用渐变
                },
                vertexShader: `
                  varying vec4 vPosition;
                  void main() {
                    vPosition = modelMatrix * vec4(position, 1.0);
                    gl_Position = projectionMatrix * viewMatrix * vPosition; // 计算最终位置
                  }
                `,
                fragmentShader: `
                  uniform mat4 modelMatrix;
                  varying vec4 vPosition;
                  uniform vec3 uMax;
                  uniform vec3 uMin;
                  uniform float uOpacity;
                  uniform float uBorderWidth;
                  uniform vec3 uLightColor;
                  uniform vec3 uColor;
                  uniform float uCircleTime;
                  uniform float uTime;
                  uniform vec3 uTopColor; // 顶部颜色
                  uniform bool uGradient;
                  
                  void main() {
                    // 转换到世界坐标
                    vec4 uMax_world = modelMatrix * vec4(uMax, 1.0);
                    vec4 uMin_world = modelMatrix * vec4(uMin, 1.0);

                    vec4 csm_DiffuseColor = vec4(uColor, uOpacity);
                    float residue = uTime - floor(uTime / uCircleTime) * uCircleTime;
                    float rate = residue / uCircleTime;
                    float lightOffset = rate * (uMax_world.y - uMin_world.y);

                    // 检查光照范围
                    if (uMin_world.y + lightOffset < vPosition.y && 
                        uMin_world.y + lightOffset + uBorderWidth > vPosition.y) {
                      csm_DiffuseColor = vec4(uLightColor, uOpacity);
                    }

                    // 根据高度计算颜色
                    if (uGradient) {
                      float rateHeight = (vPosition.y - uMin_world.y) / (uMax_world.y - uMin_world.y);
                      vec3 outColor = mix(csm_DiffuseColor.xyz, uTopColor, rateHeight * 2.0);
                      csm_DiffuseColor = vec4(outColor, uOpacity);
                    }

                    gl_FragColor = csm_DiffuseColor; // 设置输出颜色
                  }
                `,
              });

              child.material = shaderMaterial; // 应用自定义着色器材质

              // child.material.side = THREE.DoubleSide; //双面渲染
              // child.material.color.setStyle("#ff0000"); // 设置颜色为红色
              // child.material.transparent = true; // 允许透明
              // child.material.opacity = 0.5; // 设置透明度为 0.5
            }
          });
          // object.position.set(0, 0,-3); // 可根据需要设置位置
          this.modelApi.scene.add(object); // 将模型组添加到场景中
          this.modelApi.sceneAnimation();
        })
        .catch((error) => {
          console.error("模型加载失败", error);
        });

      this.$refs.model.addEventListener("click", (event) => {
        // this.modelApi.onClick(event);
      });
    },
  },
};
</script>

<style lang="scss" scopedF>
/* 粮仓标签的样式属性 */
.tag {
  box-shadow: 0 0 2px #00ffff inset;
  background: linear-gradient(#00ffff, #00ffff) left top,
    linear-gradient(#00ffff, #00ffff) left top,
    linear-gradient(#00ffff, #00ffff) right bottom,
    linear-gradient(#00ffff, #00ffff) right bottom;
  background-repeat: no-repeat;
  background-size: 1px 6px, 6px 1px;
  background-color: rgba(0, 0, 0, 0.4);
  color: #ffffff;
  font-size: 16px;
  padding: 4px 10px;
}
</style>



