<template>
    <div style="width: 500px; height: 400px;">
  
      <!-- 图表部分 -->
      <div ref="chart" style="width: 500px; height: 400px; margin-top: 20px;"></div>
    </div>
  </template>
  
  <script>
  import * as echarts from 'echarts';
  
  export default {
    name: 'FlowAnalysis',
    data() {
      return {
        chart: null,
        headerData: [
          { title: '方案名称', value: '永定河流域十年一遇设', subValue: '计洪水需堂水库调度' },
          { title: '最高水位 (m)', value: '66.60', subValue: '2024-2-11 12:00', highlight: true },
          { title: '至汛限可纳蓄量 (万m³)', value: '30', subValue: '60' },
          { title: '至防洪高可纳蓄量 (万m³)', value: '60', subValue: '' },
          { title: '最大入库流量 (m³/s)', value: '2000', subValue: '2024-2-11 12:00' },
          { title: '最大出库流量 (m³/s)', value: '2000', subValue: '2024-2-11 12:00' },
          { title: '削峰率', value: '20%', subValue: '' },
          { title: '雁翅站洪峰流量 (m³/s)', value: '2500', subValue: '2024-2-11 12:00' },
        ]
      };
    },
    mounted() {
      this.initChart();
    },
    methods: {
      initChart() {
        this.chart = echarts.init(this.$refs.chart);
  
        const option = {
          color: ['#66ccff', '#ffcc66', '#ff6666', '#66ff66', '#9966ff'], // 配置线条颜色
          tooltip: {
            trigger: 'axis',
            formatter: '{b0}: {c0} m³/s',
          },
          legend: {
            data: ['方案1出库流量', '方案2出库流量', '方案1入库流量', '方案2入库流量', '方案3入库流量'],
            // bottom: 0,
            textStyle: {
              color: '#ffffff',
            },
          },
          grid: {
            top: 20,
            left: '10%',
            right: '10%',
            bottom: '15%',
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: ['00:00', '04:00', '08:00', '12:00', '16:00', '20:00', '24:00'],
            axisLine: {
              lineStyle: {
                color: '#fff',
              },
            },
            axisLabel: {
              textStyle: {
                color: '#fff',
              },
            },
          },
          yAxis: {
            type: 'value',
            name: '流量 (m³/s)',
            min: 62,
            max: 70,
            position: 'left',
            axisLine: {
              lineStyle: {
                color: '#fff',
              },
            },
            axisLabel: {
              formatter: '{value} m³/s',
              textStyle: {
                color: '#fff',
              },
            },
            splitLine: {
              lineStyle: {
                color: 'rgba(255, 255, 255, 0.2)',
              },
            },
          },
          series: [
            {
              name: '方案1出库流量',
              type: 'line',
              data: [66, 67, 68, 67.5, 67, 66.8, 66],
              smooth: true,
            },
            {
              name: '方案2出库流量',
              type: 'line',
              data: [65, 66, 67, 66.5, 66, 65.8, 65],
              smooth: true,
            },
            {
              name: '方案1入库流量',
              type: 'line',
              data: [64, 65, 66, 65.5, 65, 64.8, 64],
              smooth: true,
            },
            {
              name: '方案2入库流量',
              type: 'line',
              data: [63.5, 64.5, 65.5, 65, 64.5, 64.2, 63.5],
              smooth: true,
            },
            {
              name: '方案3入库流量',
              type: 'line',
              data: [63, 64, 65, 64.5, 64, 63.8, 63],
              smooth: true,
            },
          ],
        };
  
        this.chart.setOption(option);
      },
    },
  };
  </script>
  
  <style scoped>
  div {
    display: flex;
    justify-content: space-around;
    color: #ffffff;
    background-color: #002b54;
    padding: 10px;
    border-radius: 5px;
  }
  
  .header-row {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  
  .header-item {
    text-align: center;
    flex: 1;
  }
  
  .header-value {
    font-size: 24px;
    font-weight: bold;
  }
  </style>
  


