<template lang="">
  <div>
    <el-upload
      style="display: inline-block"
      action=""
      accept=".xlsx,.xls"
      :auto-upload="false"
      :show-file-list="false"
      :on-change="hanlde"
    >
      <el-button size="small" type="primary">点击上传</el-button>
    </el-upload>
    <el-button
      size="small"
      type="primary"
      @click="exportFile()"
      style="margin-top: 20px"
      >导出</el-button
    >
    <el-table
      :data="tableData"
      style="width: 100%"
      border=""
      @selection-change="selectionChange"
    >
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column prop="name" label="姓名" min-width="50" align="center">
      </el-table-column>
      <el-table-column prop="phone" label="电话" min-width="50" align="center">
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { readFile, character, delay } from "./utils";
import { Loading } from "element-ui";
import xlsx from "xlsx";
export default {
  data() {
    return {
      tableData: [{ name: "张三", phone: 1234567890 }],
      selectionList: [],
    };
  },

  mounted() {},
  methods: {
    selectionChange(selection) {
      this.selectionList = selection;
    },
    async hanlde(ev) {
      let file = ev.raw;
      if (!file) return;
      let loadingInsetance = Loading.service({
        text: "请稍等",
        background: "rgba(0,0,0,.5)",
      });
      await delay(1000);
      //读取file文件
      let data = await readFile(file);
      //解析二进制数据
      let workBook = xlsx.read(data, { type: "binary" });
      // console.log("解析的数据", workBook)
      /*
      console.log(workBook):
            SheetNames:页卡
            Sheets: 基本信息
                !ref：从哪里开始到哪里结束
                !margins：信息
      */
      let worksheet = workBook.Sheets[workBook.SheetNames[0]];
      data = xlsx.utils.sheet_to_json(worksheet);
      let arr = [];
      data.forEach((item) => {
        let obj = {};
        for (let key in character) {
          if (!character.hasOwnProperty(key)) break;
          let v = character[key],
            text = v.text,
            type = v.type;
          v = item[text] || "";
          type === "string" ? (v = String(v)) : null;
          type === "number" ? (v = String(v)) : null;
          obj[key] = v;
        }

        arr.push(obj);
        this.tableData = arr;
        loadingInsetance.close();
      });
    },
    exportFile() {
      if (this.selectionList.length <= 0) {
        this.$message({
          message: "小主，请您先选择要导出的数据哦！",
          type: "warning",
          showClose: true,
        });
        return;
      }

      this.disabled = true;
      let loadingInstance = Loading.service({
        text: "小主，请您稍等片刻，奴家正在玩命处理中...",
        background: "rgba(0,0,0,.5)",
      });

      let arr = this.selectionList.map((item) => {
        return {
          姓名: item.name,
          电话: item.phone,
        };
      });
      // 将json数据变为sheet数据
      debugger;
      let sheet = xlsx.utils.json_to_sheet(arr),
        // 新建表格
        book = xlsx.utils.book_new();
      book["!merges"] = [
        // s：开始start
        // e：结束end
        // r：行row
        // c: 列columns
        { s: { r: 0, c: 1 }, e: { r: 0, c: 4 } }, // 第一行，第二列到第三列合并
        { s: { r: 0, c: 5 }, e: { r: 0, c: 9 } }, // 第一行，第三列到第四列合并
      ];
      let style = {
        alignment: {
          horizontal: "center",
          vertical: "center",
        },
        font: {
          name: "宋体",
          sz: 12,
          color: { rgb: "f50606" },
          bold: true,
          italic: false,
          underline: false,
        },
      };
      // 在表格中插入一个sheet
      xlsx.utils.book_append_sheet(book, sheet, "sheet1");
      // 通过xlsx的writeFile方法将文件写入
      xlsx.writeFile(book, `user${new Date().getTime()}.xls`);

      loadingInstance.close();
      this.disabled = false;
    },
  },
};
</script>
<style lang=""></style>



