<template>
  <div>
    <div ref="model" style="
        width: 75vw;
        height: 75vh;
        margin: 30px;
        border: 1px solid pink;
        position: relative;
      ">
      <Popover ref="popover" :top="popover.top" :left="popover.left" />
    </div>
    <SideBar>
      <div style="color: #fff">
        <div>相机x,y,z</div>
        <input type="number" v-model="camera[0]" />
        <input type="number" v-model="camera[1]" />
        <input type="number" v-model="camera[2]" />
      </div>
    </SideBar>
  </div>
</template>

<script>
import renderModel from "./js/renderModel";
import { CSS3DObject } from "three/examples/jsm/renderers/CSS3DRenderer";
import * as THREE from "three";
export default {
  components: {
    SideBar: () => import("@/components/SideBar"),
    Popover: () => import("@/modules/three/threeload/Popover.vue"),
  },
  mounted() {
    let that = this;
    that.$refs.model.addEventListener("click", (event) => {
      const rect = that.$refs.model.getBoundingClientRect();
      const offsetX = event.clientX - rect.left;
      const offsetY = event.clientY - rect.top;
      that.popover = { top: offsetY, left: offsetX };
    });
    this.init();
  },
  data() {
    return {
      camera: [4, 2, 0],
      modelApi: null,
      popover: {},
    };
  },
  watch: {
    camera(val) {
      console.log(val[0], val[1], val[2]);
      this.modelApi.camera.position.set(val[0], val[1], val[2]);
    },
  },
  methods: {
    findParent(object3d) {
      let parent = object3d;
      while (parent.parent !== null && !parent.name.includes("RoadLamp")) {
        parent = parent.parent;
      }
      return parent;
    },
    init() {
      this.modelApi = new renderModel(this.$refs.model);
      // this.modelApi.loadModel("/static/GLB/roads_arch__-_modular_pack.glb");
      // this.modelApi.loadModel("/static/GLB/datacenter.glb");
      var modelGroup = new THREE.Group();//声明一个组对象，用来添加加载成功的三维场景
      this.modelApi.loadModel("/static/GLB/roads_arch__-_modular_pack.glb")
        .then((gltf) => {
          console.log("模型加载成功", gltf);
          const scaleFactor = 0.008;


          gltf.scene.scale.set(scaleFactor, scaleFactor, scaleFactor);

          gltf.scene.traverse((object) => {
            // console.log('object对象', object.name);
            if (object.type === 'Mesh') {
              // 批量更改所有 Mesh 的材质
              object.material = new THREE.MeshLambertMaterial({
                map: object.material.map, // 获取原来材质的颜色贴图属性值
                color: object.material.color, // 读取原来材质的颜色
              });
            }
          });

          var group = gltf.scene.getObjectByName('RoadLamp');
          console.log('1', group);






          let that = this;
          group.traverse(function (obj) {
            if (obj.type === 'Mesh') {
              var label = that.modelApi.tag2D(obj.name);//把粮仓名称obj.name作为标签
              var pos = new THREE.Vector3();
              obj.getWorldPosition(pos);//获取obj世界坐标
              pos.y += 36 * 0.008;
              label.position.copy(pos);//标签标注在obj世界坐标
              modelGroup.add(label);//标签插入model组对象中    
              // console.log("标签位置:", label.position);

            }
          })


          modelGroup.add(gltf.scene);
          this.modelApi.scene.add(modelGroup); // 将模型组添加到场景中

          this.modelApi.sceneAnimation();
        })
        .catch((error) => {
          console.error("模型加载失败", error);
        });
    },
  },
};
</script>

<style lang="scss">
/* 粮仓标签的样式属性 */
.tag {
  box-shadow: 0 0 2px #00ffff inset;
  background: linear-gradient(#00ffff, #00ffff) left top,
    linear-gradient(#00ffff, #00ffff) left top,
    linear-gradient(#00ffff, #00ffff) right bottom,
    linear-gradient(#00ffff, #00ffff) right bottom;
  background-repeat: no-repeat;
  background-size: 1px 6px, 6px 1px;
  background-color: rgba(0, 0, 0, 0.4);
  color: #ffffff;
  font-size: 16px;
  padding: 4px 10px;
}
</style>



