<template>
  <div style="position: absolute; width: 1200px; height: 600px">
    <div style="width: 1200px; height: 300px">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="1200"
        height="300"
        viewBox="0 0 980 526"
      >
        <path
          id="svg_id"
          d="m0,60 900,0"
          stroke-linecap="undefined"
          stroke-linejoin="undefined"
          stroke-width="3"
          fill="#000"
          stroke="#ff0000"
        />
        <g>
          <image x="-25" y="-25" width="50" height="50" xlink:href="./水滴.png">
            <animateMotion dur="4s" begin="0s" repeatCount="indefinite">
              <mpath xlink:href="#svg_id" />
            </animateMotion>
          </image>
        </g>
      </svg>
    </div>
    <div style="position: absolute; width: 1200px; height: 300px">
      <div class="moving-image"></div>
    </div>
  </div>
</template>

<script>
export default {};
</script>

<style lang="scss" scoped>
.moving-image {
  width: 50px;
  height: 50px;
  background: url("./水滴.png") no-repeat center center;
  background-size: cover;
  position: absolute;
  /* 定义路径 */
  offset-path: path("m0,60 900,0");
  /* 自动旋转 */
  offset-rotate: auto;
  /* 动画定义 */
  animation: moveAlongPath 4s infinite linear;
}

@keyframes moveAlongPath {
  0% {
    offset-distance: 0%;
  }
  100% {
    offset-distance: 100%;
  }
}
</style>



