<template>
    <div>
        <div id="mapContainer" style="width: 100%; height: 400px;"></div>
        <div>
            <input v-model="start" placeholder="输入起点" />
            <input v-model="end" placeholder="输入终点" />
            <button @click="searchPath">搜索路径</button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            map: null,
            planning: null,
            start: '',
            end: '',
        };
    },
    mounted() {
        if (typeof BMap === 'object' && typeof BMap.Map === 'function') {
            this.initMap();
        } else {
            // 百度地图 API 还未加载完成，可以在这里添加加载完成的回调
            alert('百度地图 API 加载失败！');
        }
        this.initMap();
    },
    methods: {
        initMap() {
            // 初始化地图
            this.map = new BMap.Map('mapContainer');
            this.map.centerAndZoom(new BMap.Point(116.404, 39.915), 11);

            // 初始化规划服务
            this.planning = new BMap.PlanningService(this.map, {
                enableWalking: true, // 是否启用步行规划
                enableDriving: true, // 是否启用驾车规划
                enableTransit: true, // 是否启用公交规划
            });
        },
        searchPath() {
            if (!this.start || !this.end) {
                alert('请输入起点和终点！');
                return;
            }

            const startPoint = new BMap.Point(116.404, 39.915); // 这里只是示例坐标，实际应该使用用户输入的坐标
            const endPoint = new BMap.Point(116.404, 39.915); // 同上

            const drivingPlan = this.planning.driving(startPoint, endPoint);
            drivingPlan.search((err, result) => {
                if (err) {
                    alert('搜索路径失败：' + err);
                    return;
                }

                if (result && result.getNumRoutes() > 0) {
                    // 展示第一条路径
                    this.map.clearOverlays(); // 清除地图上的覆盖物
                    const route = result.getRoute(0);
                    route.setMap(this.map);
                } else {
                    alert('未找到路径');
                }
            });
        },
    },
};
</script>

<style scoped>
#mapContainer {
    width: 100%;
    height: 400px;
}
</style>


