<template lang="">
  <div>进度条</div>
</template>
<script>
import NProgress from "nprogress";
import "nprogress/nprogress.css";
export default {
  data() {
    return {};
  },
  mounted() {
    NProgress.start();
    NProgress.set(0.0); // Sorta same as .start()
    NProgress.set(0.4);
    NProgress.inc();
    // NProgress.set(1.0);     // Sorta same as .done()
    NProgress.done();
  },
  methods: {},
};
</script>
<style lang=""></style>



