<template>
  <div id="cesiumContainer">
    <div ref="startRef" id="start"></div>
    <div ref="endRef" id="end"></div>
  </div>
</template>
<script>
import LeaderLine from "leader-line-vue";
export default {
  data() {
    return {
      LeaderLine: null,
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.LeaderLine = LeaderLine.setLine(
        document.getElementById("start"),
        document.getElementById("end"),
        // { startPlug: "disc", endPlug: "disc", color: "red", size: 2 },
        // {
        //   color: "#000000",
        //   size: 2,
        //   path: "grid", //straight arc fluid magnet grid  连接线条的类型
        //   // endPlug: "arrow1", // behind arrow1
        //   // endPlugSize: 1,
        //   // middleLabel: LeaderLine.obj.pathLabel("blank", { color: "black" }),//中间标签

        //   // startSocket: "left",
        //   // endSocket: "right",
        //   //拉动引线 [+右/-左   +下/-上]
        //   // startSocketGravity: [0, -5],
        //   // endSocketGravity: [0, -10],
        // }

        {
          startPlugColor: "#ff3792", // 渐变色开始色
          endPlugColor: "#fff386", // 渐变色结束色
          gradient: true, // 使用渐变色
          dash: {
            // 虚线样式
            animation: true, // 让线条滚动起来
          },
          path: "grid",
          endSocket: "top",
          startSocket: "bottom",
          endPlug: "arrow2",
          // hide: true,
        }
      );
    });
  },
  destroyed() {
    this.LeaderLine.remove();
  },
  methods: {},
};
</script>
<style scoped>
#cesiumContainer {
  width: 100%;
  height: 100%;
  position: relative;
}

#start {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 10px;
  left: 10px;
  /* background-color: red; */
  border: 1px solid red;
}

#end {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 200px;
  left: 200px;
  /* background-color: red; */
  border: 1px solid red;
}
</style>



