以下是修改后的代码： ```html
<template>
  <div>
    <img src="/static/电风扇.png" alt="" style="width: 20px; height: 20px" />
    <div ref="canvasContainer" style="width: 80vw; height: 75vh"></div>
    <Popover ref="popover" :top="popover.top" :left="popover.left" />
  </div>
</template>

<script>
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import Skybox from "./js/Skybox"; // 引入 Skybox 类
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader.js";
import {
  Scene,
  PerspectiveCamera,
  AxesHelper,
  WebGLRenderer,
  Camera,
  SRGBColorSpace,
  AmbientLight,
  Raycaster,
  Vector2,
  BoxHelper,
  Color,
  Object3D,
} from "three";
export default {
  name: "ThreeModelViewer",
  components: {
    Popover: () => import("./Popover.vue"),
  },
  data() {
    return {
      popover: {},
    };
  },
  mounted() {
    this.initThree();
  },
  methods: {
    findParent(object3d) {
      let parent = object3d;
      while (parent.parent !== null && !parent.name.includes("rack")) {
        parent = parent.parent;
      }
      return parent;
    },

    initThree() {
      // 创建场景
      const scene = new THREE.Scene();
      const rect = this.$refs.canvasContainer.getBoundingClientRect();
      // 创建相机
      const camera = new THREE.PerspectiveCamera(
        25,
        rect.width / rect.height,
        1,
        2000
      );

      // 设置相机位置
      camera.position.set(4, 2, -3);
      // 设置相机方向
      camera.lookAt(0, 0, 0);

      // 创建渲染器
      const renderer = new THREE.WebGLRenderer({
        logarithmicDepthBuffer: true,
        antialias: true,
        alpha: true,
        precision: "mediump",
        premultipliedAlpha: true,
      });
      renderer.setSize(rect.width, rect.height);

      renderer.clearDepth();
      // 启用渲染器的阴影映射功能（shadowMap.enabled = true），这样可以在场景中生成和渲染阴影
      renderer.shadowMap.enabled = true;
      // 设置渲染器的输出颜色空间为 SRGBColorSpace，这将使材质显示更亮，同时也会影响环境贴图的效果。
      renderer.outputColorSpace = THREE.SRGBColorSpace; // 可以看到更亮的材质，同时这也影响到环境贴图。
      this.$refs.canvasContainer.appendChild(renderer.domElement);

      // 添加光源
      const ambientLight = new THREE.AmbientLight(0xffffff, 0.2); // 环境光
      scene.add(ambientLight);

      const pointLight = new THREE.PointLight(0xffffff, 1, 100); // 点光源
      pointLight.position.set(10, 10, 10);
      scene.add(pointLight);

      // 创建定向光
      const light = new THREE.DirectionalLight(0xffffff,1);
      light.position.set(-1.44, 2.2, 1);
      light.castShadow = true;
      light.visible = true ;
      // light.shadow.camera.top = 180;
      // light.shadow.camera.bottom = -100;
      // light.shadow.camera.left = -120;
      // light.shadow.camera.right = 400;
      // light.shadow.camera.near = 0.1;
      // light.shadow.camera.far = 400;
      // light.shadow.mapSize.set(rect.width, rect.height);
      scene.add(light);
      // 创建和加载天空盒
      const skybox = new Skybox(scene, "/static/skybox/night/", ".jpg"); // 设置路径和格式 dusk night daytime
      skybox.loadSkybox(); // 加载天空盒


      // const directionalLight = new THREE.DirectionalLight("#fff", 1); // 设置平行光的强度1-10
      // directionalLight.position.set(-1.44, 2.2, 1);
      // directionalLight.castShadow = true;
      // directionalLight.visible = true;
      // scene.add(directionalLight);


      // 配置 DRACOLoader
      const dracoLoader = new DRACOLoader();
      dracoLoader.setDecoderPath("/static/draco/"); // 指定 Draco 解码器的路径

      // 创建 GLTFLoader 实例加载模型
      const loader = new GLTFLoader();
      loader.setDRACOLoader(dracoLoader); // 将 DRACOLoader 实例传递给 GLTFLoader

      let animateTexture = null;
      // 加载 GLB 文件
      loader.load(
        "/static/GLB/plane.glb",
        function (gltf) {
          const texture = gltf.scene.children[0].material.map;

          // 纹理动画函数
          const planeAnimate = (texture) => {
            texture.wrapS = THREE.RepeatWrapping;
            texture.wrapT = THREE.RepeatWrapping;

            return {
              fun: () => {
                const count = texture.repeat.y;
                if (count <= 10) {
                  texture.repeat.x += 0.01;
                  texture.repeat.y += 0.02;
                } else {
                  texture.repeat.x = 0;
                  texture.repeat.y = 0;
                }
              },
            };
          };

          // 动画
          animateTexture = planeAnimate(texture);

          // 设置模型颜色
          // const color = "red"; // 示例颜色，绿色
          // const opacity = 0.4; // 示例透明度
          // gltf.scene.traverse((child) => {
          //   if (child.isMesh) {
          //     child.material = new THREE.MeshPhongMaterial({
          //       // side 表示双面渲染。
          //       side: THREE.DoubleSide,
          //       // transparent 表示开启透明。
          //       transparent: true,
          //       // depthTest 表示深度测试
          //       depthTest: false,
          //       // depthWrite 表示深度写入，设置为 true 表示不会被鼠标穿透。
          //       depthWrite: true, // 无法被选择，鼠标穿透
          //       // color 表示颜色，使用传入的颜色值。
          //       color: new THREE.Color(color),
          //       // opacity 表示透明度，使用传入的透明度值。
          //       opacity: opacity,
          //     });
          //   }
          // });

          scene.add(gltf.scene);
          gltf.scene.scale.set(0.0001 * 3, 0.0001 * 3, 0.0001 * 3);
          gltf.scene.position.set(0, 0, 0);
        },

        undefined,
        function (error) {
          console.error(error);
        }
      );
      let that = this;
      loader.load(
        "/static/GLB/datacenter.glb",
        function (gltf) {
          // gltf.scene.traverse((child) => {
          //   if (child.isMesh) {
          //     child.receiveShadow = false; // 设置接收阴影的状态（可选）

          //     // child.frustumCulled = false;// 同时将对象的 castShadow 属性设置为 true，表示开启投影阴影。
          //     child.castShadow = true; // 设置投射阴影的状态
          //   }
          // });

          // gltf.scene.traverse((v) => {
          //   if (v.isMesh && v.material) {
          //     const { name, color, map, wireframe, depthWrite, opacity } =
          //       v.material;
          //     v.material = new THREE.MeshMatcapMaterial({
          //       map: map,
          //       // transparent: true,
          //       emissive: 0x0055ff, // 自发光颜色
          //       color,
          //       name,
          //     });
          //     depthWrite ? (v.material.depthWrite = depthWrite) : "";
          //     opacity ? (v.material.opacity = opacity) : "0.2";
          //     v.castShadow = true; // 开启阴影投射
          //     v.receiveShadow = true; // 开启接收阴影
          //     wireframe ? (v.material.wireframe = wireframe) : "";
          //     v.material.side = THREE.DoubleSide;
          //   }
          // });

          scene.add(gltf.scene);
          gltf.scene.scale.set(0.2, 0.2, 0.2);
          gltf.scene.position.set(0, 0, 0);

          const raycaster = new THREE.Raycaster();
          const mouse = new THREE.Vector2();
          const highlightBox = new BoxHelper(
            new Object3D(),
            new Color(0x00ffff)
          );

          that.$refs.canvasContainer.addEventListener("click", (event) => {
            const rect = that.$refs.canvasContainer.getBoundingClientRect();
            const offsetX = event.clientX - rect.left;
            const offsetY = event.clientY - rect.top;
            that.popover = { top: offsetY, left: offsetX };
            const data = { name: "Popover Title" }; // 传递的数据
            that.$refs.popover.setShow(true, data); // 调用 setShow 方法显示 Popover

            mouse.x = (offsetX / rect.width) * 2 - 1;
            mouse.y = -(offsetY / rect.height) * 2 + 1;

            raycaster.setFromCamera(mouse, camera);
            const intersects = raycaster.intersectObject(gltf.scene, true);

            if (intersects.length > 0) {
              scene.remove(highlightBox);
              const selectedObject = intersects[0].object || {};

              highlightBox.setFromObject(that.findParent(selectedObject));
              scene.add(highlightBox);
            } else {
              scene.remove(highlightBox);
            }
          });
        },

        undefined,
        function (error) {
          console.error(error);
        }
      );
      // 创建摄像机控制器
      const controls = new OrbitControls(camera, renderer.domElement);
      controls.enableDamping = false;
      controls.screenSpacePanning = false;
      controls.minDistance = 2;
      controls.maxDistance = 1000;
      // controls.addEventListener("change", () => {
      //   renderer.render(scene, camera);
      // });

      // 渲染循环
      function animate() {
        requestAnimationFrame(animate); // 请求下一个动画帧
        renderer.render(scene, camera); // 渲染场景
        controls.update(); // 更新控制器
        if (animateTexture) animateTexture.fun();
      }
      animate(); // 启动动画循环
    },
  },
};
</script>

