<template>
  <!-- 刻度尺 -->
  <div class="box">
    <div class="d-flex">
      <div>刻度尺</div>
    </div>
    <div class="rule">
      <div class="rule_cur" :style="{ width: curWidth + '%' }"></div>
      <div
        class="rule_sign rule_start shadow"
        :class="{ active: activeStatus(0), shadow: activeStatus(0) }"
        @click="setStatus(0)"
      ></div>
      <div
        class="rule_sign"
        v-for="i in ruleData.length"
        :key="i"
        :style="{ left: (i / ruleData.length) * 99 + '%' }"
        :class="{ active: activeStatus(i), shadow: activeStatus(i) }"
        @click="setStatus(i)"
      >
        <div class="rule_tips">
          {{ ruleData[i] }}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    curData: {
      type: Number,
      require: true,
      default: 0,
    },
    ruleData: {
      type: Array,
      require: true,
      default: () => ["0", "1天", "2", "3", "4", "5", "6", "9天"],
    },
  },
  data() {
    return {};
  },
  computed: {
    curWidth() {
      return (this.curData / this.ruleData.length) * 99;
    },
  },
  methods: {
    activeStatus(i) {
      return this.curData == i;
    },
    setStatus(i) {
      this.$emit("update:curData", i);
    },
  },
};
</script>
<style lang="scss" scoped>
.box {
  width: 300px;
  .d-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0;
  }
  .rule {
    width: 100%;
    height: 10px;
    background-color: #ccc;
    border-radius: 10px;
    position: relative;
    &_cur {
      position: absolute;
      left: 0;
      top: 0;
      height: 10px;
      background-color: aquamarine;
      border-radius: 10px 0 0 10px;
      transition: width 0.5s;
    }
    &_start {
      left: 0;
    }
    &_sign {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 2px;
      height: 26px;
      background-color: #ccc;
      cursor: pointer;
      &::after {
        content: "";
        position: absolute;
        width: 24px;
        height: 26px;
        left: 50%;
        transform: translateX(-50%);
        background-color: transparent;
      }
      .rule_tips {
        position: absolute;
        bottom: -140%;
        left: 0;
        transform: translateX(-40%);
        font-size: 14px;
        min-width: 25px;
        text-align: center;
        display: none;
      }
    }
    &_sign:nth-of-type(3),
    &_sign:nth-last-child(2) {
      .rule_tips {
        display: block;
      }
    }
    &_sign:nth-of-type(2),
    &_sign:last-of-type {
      &:not(.active) {
        opacity: 0;
      }
    }
    &_sign.active {
      width: 6px;
      background: #fbfbfb;
      transition: background 0.4s ease-in;
    }
  }
}
.shadow {
  box-shadow: 0 1px 5px #9a9a9a;
}
</style>


