// 核心代码
<template>
  <el-tabs type="border-card" tab-position="left" v-model="activeName">
    <el-tab-pane label="刻度尺" name="刻度尺">
      <com
        :curData="0"
        :ruleData="['50w', '100w', '200w', '400w', '800w', '1600w', '3200w']"
      />
    </el-tab-pane>
    <el-tab-pane label="ImageViewer" name="ImageViewer">
      <div style="width: 509px; height: 500px">
        <image-viewer
          imageUrl="https://profile-avatar.csdnimg.cn/2c10a3762fec44aea81f7c8fc58921af_qq_35713752.jpg!1"
        />
      </div>
    </el-tab-pane>
    <el-tab-pane
      label="ImageViewer1"
      name="ImageViewer1"
      style="width: 1009px; height: 700px"
    >
      <div
        style="
          width: 509px;
          height: 500px;
          border: 1px solid red;
          margin: 100px 50px;
        "
      >
        <img
          v-scale
          src="https://img0.baidu.com/it/u=2616823501,3205478532&fm=253&fmt=auto&app=138&f=JPEG?w=509&h=500"
          style="width: 100%; height: 100%; opacity: 0.5"
        />
      </div>
    </el-tab-pane>

    <el-tab-pane
      label="ImageViewer5"
      name="ImageViewer5"
      style="width: 1018px; height: 1000px; overflow: hidden"
    >
      <com12 />
    </el-tab-pane>

    <el-tab-pane
      label="ImageViewer6"
      name="ImageViewer6"
      style="width: 1018px; height: 1000px; overflow: hidden"
    >
      <v-zoomer
        ref="zoomer"
        style="width: 800px; height: 500px; border: solid 1px silver"
        :aspect-ratio="imageAspectRatio"
        :max-scale="10"
        :zooming-elastic="false"
        :zoomed.sync="zoomed"
      >
        <img
          src="https://img0.baidu.com/it/u=2616823501,3205478532&fm=253&fmt=auto&app=138&f=JPEG?w=509&h=500"
          style="object-fit: contain; width: 100%; height: 100%"
          @load="onImageLoad"
        />
        <img
          src="./小车汽车.png"
          alt=""
          class="pimg"
          style="top: 25px; left: 225px"
        />
      </v-zoomer>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
export default {
  components: {
    com: () => import("./com-1.vue"),
    ImageViewer: () => import("./ImageViewer.vue"),
    com12: () => import("./com12.vue"),
    vZoomer: () => import("./zoomer/vue-zoomer.vue"),
  },
  data() {
    return {
      activeName: "ImageViewer6",

      zoomed: false,
      imageAspectRatio: 1,
    };
  },

  methods: {
    onImageLoad(e) {
      const img = e.target;
      this.imageAspectRatio = img.naturalWidth / img.naturalHeight;
    },
  },
};
</script>
<style scoped>
.pimg {
  position: absolute;
  top: 25px;
  left: 225px;
  width: 24px;
}
</style>



