<template>
  <div class="scroll-container">
    <div class="sidebar">
      <ul>
        <li
          v-for="item in directoryItems"
          :key="item.id"
          :class="{ active: selectedId === item.id }"
        >
          <a href="#" @click.prevent="selectItem(item)">
            {{ item.title }}
          </a>
        </li>
      </ul>
    </div>
    <div class="scrollcontent" @scroll="onScroll" ref="scrollcontent">
      <div
        v-for="item in directoryItems"
        :key="item.id"
        :id="'scrollcontent-' + item.id"
        class="scrollcontent-section"
      >
        <h2>{{ item.title }}</h2>
        <p>{{ item.body }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      directoryItems: [
        { id: 1, title: "Introduction", body: "This is the introduction." },
        {
          id: 2,
          title: "Getting Started",
          body: "This is how you get started.",
        },
        {
          id: 3,
          title: "Advanced Topics",
          body: "This covers advanced topics.",
        },
      ],
      selectedId: null,
    };
  },
  methods: {
    selectItem(item) {
      this.selectedId = item.id;
      this.$nextTick(() => {
        const content = this.$refs.scrollcontent; // 获取 content 容器
        const element = document.getElementById(`scrollcontent-${item.id}`); // 获取目标元素
        if (element && content) {
          const offsetTop = element.offsetTop; // 计算目标元素相对于 .content 顶部的距离
          content.scrollTo({ top: offsetTop, behavior: "smooth" }); // 在 .content 容器内滚动
        }
      });
    },
    onScroll() {
      const scrollcontent = this.$refs.scrollcontent;
      const sections = scrollcontent.querySelectorAll(".scrollcontent-section");
      let currentId = null;

      sections.forEach((section) => {
        const rect = section.getBoundingClientRect();
        if (rect.top >= 0 && rect.top < window.innerHeight) {
          console.log(section.id, rect);
          currentId = section.id.replace("scrollcontent-", "");
        }
      });

      this.selectedId = Number(currentId);
    },
  },
  mounted() {
    // if (this.directoryItems.length > 0) {
    //   this.selectItem(this.directoryItems[0]);
    // }
  },
};
</script>

<style scoped>
.scroll-container {
  /* position: relative; */
  display: flex;
  width: 900px;
  height: calc(100vh - 300px);
}

.sidebar {
  /* position: fixed; */
  top: 100px;
  right: 0;
  width: 25%;
  padding: 1rem;
  background-color: #f4f4f4;
  overflow-y: auto; /* Scroll if scrollcontent overflows */
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar li {
  margin-bottom: 0.5rem;
}

.sidebar li.active a {
  font-weight: bold;
  color: #007bff;
}

.scrollcontent {
  width: 75%;
  padding: 1rem;
  margin-right: 25%; /* Offset to account for fixed sidebar width */
  height: calc(100vh - 300px); /* Adjust to fit the viewport */
  overflow-y: scroll;
}

.scrollcontent-section {
  margin-bottom: 2rem;
  padding: 1rem;
  border: 1px solid #ddd;
  height: 100vh;
}
</style>



