<template>
  <div class="right-page">
    <!-- 4条svg线条 -->
    <div class="container">
      <div class="line-wrap">
        <svg
          v-for="(points, index) in svgData[currentIndex]"
          :key="index"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          xml:space="preserve"
          class="circle-load-rect-svg"
          viewbox="0 0 600 400"
        >
          <polyline
            :points="points"
            fill="none"
            class="g-rect-path"
            :stroke="strokes[index].path"
          />
          <polyline
            :points="points"
            fill="none"
            class="g-rect-fill"
            :stroke="strokes[index].fill"
          />
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      TabList: ["联通互通", "云边协作", "数字可视化", "系统对接"],
      currentIndex: 0,
      svgData: [
        [
          "200,0 200,30 930,30 930,97",
          "180,0 180,50 753,50 753,97",
          "160,0 160,70 580,70 580,97",
          "140,0 140,90 405,90 405,96",
        ],
        [
          "530,0 530,30 930,30 930,97",
          "510,0 510,50 753,50 753,97",
          "490,0 490,70 580,70 580,97",
          "470,0 470,90 405,90 405,96",
        ],
        [
          "860,0 860,97 930,97 930,97",
          "840,0 840,50 840,97 753,97",
          "820,0 820,83 580,83 580,97",
          "800,0 800,70 405,70 405,96",
        ],
        [
          "1190,0 1190,97 930,97 930,97",
          "1170,0 1170,83 753,83 753,97",
          "1150,0 1150,70 580,70 580,97",
          "1130,0 1130,58 405,58 405,96",
        ],
      ],
      strokes: [
        { path: "#007084", fill: "#00e9f9" },
        { path: "#805c2c", fill: "#feee3c" },
        { path: "#19875c", fill: "#3aeca1" },
        { path: "#18808e", fill: "#36f4fb" },
      ],
    };
  },
  methods: {
    activeClick(index) {
      this.currentIndex = index;
    },
  },
};
</script>

<style scoped lang="scss">
.right-page {
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50% 90%;
  position: relative;
}

.container {
  width: 100%;
  margin-top: 100px;
}

.line-wrap {
  width: 1350px;
  margin: 0 auto;
  position: relative;

  svg {
    position: absolute;
    top: 0;
    &.circle-load-rect-svg {
      width: 1350px;
      margin: 10px;
    }
  }

  .g-rect-path,
  .g-rect-fill {
    fill: none;
    stroke-width: 3;
    stroke-linejoin: round;
    stroke-linecap: round;
  }

  .g-rect-fill {
    stroke-width: 5;
    stroke-dasharray: 3, 900;
    stroke-dashoffset: -100;
    animation: lineMove 3s cubic-bezier(0, 0, 0.74, 0.74) infinite;
  }
}

@keyframes lineMove {
  0% {
    stroke-dashoffset: -850;
  }
  100% {
    stroke-dashoffset: 0;
  }
}
</style>



