export default {
  bind(el) {
    // 记录初始的鼠标位置和元素的位置
    let startX, startY, initialLeft, initialTop;

    // 鼠标按下事件处理函数
    function onMouseDown(event) {
      // 记录鼠标按下时的位置
      startX = event.clientX;//用于获取相对于视口的水平坐标（即页面的可视区域）上的鼠标指针位置
      startY = event.clientY;

      // 记录元素的初始位置
      initialLeft = el.offsetLeft;//用于获取元素相对于其偏移父元素的左边界的偏移像素
      initialTop = el.offsetTop;
      console.log("startX", startX, "startY", startY);
      console.log("initialLeft", initialLeft, "initialTop", initialTop);
      // 绑定鼠标移动和释放事件
      document.addEventListener("mousemove", onMouseMove);
      document.addEventListener("mouseup", onMouseUp);
    }

    // 鼠标移动事件处理函数
    function onMouseMove(event) {
      // 计算鼠标移动的距离
      const deltaX = event.clientX - startX;
      const deltaY = event.clientY - startY;
      console.log("deltaX", deltaX, "deltaY", deltaY);
      // 更新元素的位置
      el.style.left = `${initialLeft + deltaX}px`;
      el.style.top = `${initialTop + deltaY}px`;
    }

    // 鼠标释放事件处理函数
    function onMouseUp() {
      // 移除鼠标移动和释放事件的监听器
      document.removeEventListener("mousemove", onMouseMove);
      document.removeEventListener("mouseup", onMouseUp);
    }

    // 添加鼠标按下事件监听器
    el.addEventListener("mousedown", onMouseDown);

    // 清理事件监听器时使用
    el._onMouseDown = onMouseDown;
    el._onMouseMove = onMouseMove;
    el._onMouseUp = onMouseUp;
  },

  unbind(el) {
    // 移除鼠标按下事件监听器
    el.removeEventListener("mousedown", el._onMouseDown);

    // 移除全局的鼠标移动和释放事件监听器
    document.removeEventListener("mousemove", el._onMouseMove);
    document.removeEventListener("mouseup", el._onMouseUp);
  },
};



