<template>
    <div ref="container" class="three-container"></div>
  </template>
  
  <script>
  import * as THREE from 'three';
  import { CSS3DRenderer, CSS3DObject } from 'three/examples/jsm/renderers/CSS3DRenderer';
  
  export default {
    name: 'ThreeDLabel',
    mounted() {
      this.initThreeJS();
    },
    methods: {
      initThreeJS() {
        // 创建场景、相机和渲染器
        const scene = new THREE.Scene();
        const camera = new THREE.PerspectiveCamera(75, this.$refs.container.clientWidth / this.$refs.container.clientHeight, 0.1, 1000);
        const renderer = new THREE.WebGLRenderer();
        renderer.setSize(this.$refs.container.clientWidth, this.$refs.container.clientHeight);
        this.$refs.container.appendChild(renderer.domElement);
  
        // 创建 CSS3DRenderer
        const cssRenderer = new CSS3DRenderer();
        cssRenderer.setSize(this.$refs.container.clientWidth, this.$refs.container.clientHeight);
        cssRenderer.domElement.style.position = 'absolute';
        cssRenderer.domElement.style.top = 0;
        this.$refs.container.appendChild(cssRenderer.domElement);
  
        // 创建一个立方体
        const geometry = new THREE.BoxGeometry(1, 1, 1);
        const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
        const cube = new THREE.Mesh(geometry, material);
        scene.add(cube);
  
        // 添加 3D 文字标签
        const labelDiv = document.createElement('div');
        labelDiv.style.width = '2px';
        labelDiv.style.height = '1px';
        labelDiv.style.backgroundColor = 'rgba(255, 255, 255, 0.8)';
        labelDiv.style.color = 'black';
        labelDiv.style.fontSize = '0.4px';
        labelDiv.style.textAlign = 'center';
        // labelDiv.style.lineHeight = '1px';
        labelDiv.innerHTML = 'Hello, 3D!';

  
        const label = new CSS3DObject(labelDiv);
        label.position.set(0, 0, 0); // 设置标签位置
        scene.add(label);
  
        // 设置相机位置
        camera.position.z = 5;
  
        // 动画循环
        const animate = () => {
          requestAnimationFrame(animate);
          cube.rotation.x += 0.01;
          cube.rotation.y += 0.01;
  
          renderer.render(scene, camera);
          cssRenderer.render(scene, camera); // 渲染 CSS3DObject
        };
  
        animate();
      },
    },
  };
  </script>
  
  <style scoped>
  .three-container {
    position: relative;
    width: 100%;
    height: 50vh; /* 设置为全屏 */
  }
  </style>
  


