import * as echarts from "echarts";

// 计算交点
// 计算二次贝塞尔曲线的交点
export function findIntersection(yAxisData, yLine) {
  const intersections = [];

  // 确保 yAxisData 长度至少为 2
  if (yAxisData.length < 2) return intersections;

  // 创建 x 轴数据
  const xAxisData = yAxisData.map((_, index) => index);

  for (let i = 0; i < xAxisData.length - 1; i++) {
    const x1 = xAxisData[i];
    const x2 = xAxisData[i + 1];
    const y1 = yAxisData[i];
    const y2 = yAxisData[i + 1];

    // 处理 y1 和 y2 相等的情况，避免除零错误
    if (y1 === y2) {
      if (y1 === yLine) {
        // 如果水平线与线段平行且在同一水平线上，则返回整个线段
        intersections.push([x1, yLine]);
        intersections.push([x2, yLine]);
      }
      continue;
    }

    // 检查线段是否与 yLine 相交
    if ((y1 < yLine && y2 > yLine) || (y1 > yLine && y2 < yLine)) {
      // 计算交点
      const xIntersect = x1 + ((yLine - y1) * (x2 - x1)) / (y2 - y1);
      intersections.push([xIntersect, yLine]);
    }
  }

  return intersections;
}





