// 核心代码
<template>
    <el-tabs type="border-card" tab-position="left" v-model="activeName">
        <el-tab-pane label="map1" name="map1">
            <map1 v-if="activeName=='map1'"></map1>
        </el-tab-pane>
        <el-tab-pane label="map2" name="map2">
            <map2 v-if="activeName=='map2'"></map2>
        </el-tab-pane>
        <el-tab-pane label="map3" name="map3">
            <map3 v-if="activeName=='map3'"></map3>
        </el-tab-pane>
        <el-tab-pane label="map4" name="map4">
            <map4 v-if="activeName=='map4'"></map4>
        </el-tab-pane>
    </el-tabs>
</template>

<script>
export default {
    components: {
        map1: () => import('./map1.vue'),
        map2: () => import('./map2.vue'),
        map3: () => import('./map3.vue'),
        map4: () => import('./map4.vue')
    },
    data() {
        return {
            activeName: 'map4'
        }
    },
}
</script>
<style scoped></style>



