<template>
    <div
      class="popover"
      :style="{
        top: `${top}px`,
        left: `${left}px`,
        display: isShow ? 'inline-block' : 'none'
      }"
    >
      <div class="popover-title">
        {{ dataRef.name }}
      </div>
      <div class="popover-content">
        {{ dataRef.content }}
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'Popover',
    props: {
      top: {
        type: Number,
        default: 0
      },
      left: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        isShow: false,
        dataRef: {}
      };
    },
    methods: {
      setShow(visible, data) {
        this.isShow = visible;
        if (data) {
          this.dataRef = data;
        }
      }
    }
  };
  </script>
  
  <style scoped>
  .popover {
    position: absolute;
    background-color: rgb(29 78 216 / 0.6);
    border-radius: 5px;
    font-size: 16px;
    color: #fff;
  }
  .popover-title {
    padding: 12px;
    border-bottom: 1px solid #fff;
  }
  .popover-content {
    padding: 24px;
  }
  </style>
  


