<template>
  <div>
    <div>
      <label for="fov">视场角 (FOV): </label>
      <input type="number" id="fov" v-model="fov" @change="updateCamera" />
    </div>
    <div>
      <label for="cameraPositionX">相机位置 X: </label>
      <input
        type="number"
        id="cameraPositionX"
        v-model="cameraPosition.x"
        @change="updateCamera"
      />
    </div>
    <div>
      <label for="cameraPositionY">相机位置 Y: </label>
      <input
        type="number"
        id="cameraPositionY"
        v-model="cameraPosition.y"
        @change="updateCamera"
      />
    </div>
    <div>
      <label for="cameraPositionZ">相机位置 Z: </label>
      <input
        type="number"
        id="cameraPositionZ"
        v-model="cameraPosition.z"
        @change="updateCamera"
      />
    </div>
    <div ref="canvasContainer"></div>
  </div>
</template>

<script>
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import {
  Scene,
  PerspectiveCamera,
  AxesHelper,
  WebGLRenderer,
  Camera,
  SRGBColorSpace,
  AmbientLight,
  Raycaster,
  Vector2,
} from "three";

export default {
  name: "ThreeModelViewer",
  data() {
    return {
      fov: 30, // 初始视场角
      cameraPosition: {
        x: 6,
        y: 2,
        z: -3,
      },
    };
  },
  mounted() {
    this.initThree();
  },
  methods: {
    initThree() {
      // 创建场景
      const scene = new THREE.Scene();

      const ambient = new THREE.AmbientLight(0xffffff, 0.4);
      scene.add(ambient);
      scene.add(new AmbientLight("#fff", 0.4));
      scene.add(new AmbientLight("#fff", 0.4));
      scene.add(new AmbientLight("#fff", 0.4));
      scene.add(new AmbientLight("#fff", 0.4));
      // scene.add(new AmbientLight("#fff", 0.4));
      // scene.add(new AmbientLight("#fff", 0.4));
      // scene.add(new AmbientLight("#fff", 0.4));
      // scene.add(new AmbientLight("#fff", 0.4));
      // scene.add(new AmbientLight("#fff", 0.4));
      // scene.add(new AmbientLight("#fff", 0.4));

      // 创建了一个定向光（DirectionalLight）对象，光的颜色为白色（0xffffff）。
      const light = new THREE.DirectionalLight(0xffffff);
      // 设置定向光的位置为 (0, 200, 100)，表示光源的位置在场景中的 (x:0, y:200, z:100) 处
      // light.position.set(-23, 2.5, -0.5);
      light.position.set(0, 200, 100);
      // 将定向光设置为可以产生阴影（castShadow = true）
      light.castShadow = true;
      // 配置定向光的阴影相机（shadow.camera）的参数，包括视锥体的上下左右边界、近平面和远平面的距离，这些参数将影响阴影的投射范围和清晰度
      light.shadow.camera.top = 180;
      light.shadow.camera.bottom = -100;
      light.shadow.camera.left = -120;
      light.shadow.camera.right = 400;
      light.shadow.camera.near = 0.1;
      light.shadow.camera.far = 400;
      // 设置阴影贴图的大小为 1024x1024，通过调整 mapSize 属性可以使阴影显示更清晰。
      // 设置mapSize属性可以使阴影更清晰，不那么模糊
      light.shadow.mapSize.set(1024, 1024);

      scene.add(light);
      // 创建相机
      this.camera = new THREE.PerspectiveCamera(
        this.fov,
        window.innerWidth / window.innerHeight,
        1,
        2000
      );

      // 设置相机位置
      this.camera.position.set(
        this.cameraPosition.x,
        this.cameraPosition.y,
        this.cameraPosition.z
      );

      // 创建渲染器
      const renderer = new THREE.WebGLRenderer({ antialias: true });
      renderer.setSize(window.innerWidth / 1.2, window.innerHeight / 1.2);
      this.$refs.canvasContainer.appendChild(renderer.domElement);

      // 配置 DRACOLoader
      const dracoLoader = new DRACOLoader();
      dracoLoader.setDecoderPath("/static/draco/"); // 指定 Draco 解码器的路径

      // 加载模型
      const loader = new GLTFLoader();
      loader.setDRACOLoader(dracoLoader); // 将 DRACOLoader 实例传递给 GLTFLoader

      loader.load(
        "/static/GLB/tunnel_road.glb",
        (gltf) => {
          scene.add(gltf.scene);
          gltf.scene.scale.set(1.2, 1.2, 1.2);
          gltf.scene.traverse((child) => {
            if (child.isMesh) {
              child.castShadow = true; // 开启阴影投射
              child.receiveShadow = true; // 开启接收阴影
            }
          });
        },
        undefined,
        (error) => {
          console.error(error);
        }
      );

      const controls = new OrbitControls(this.camera, renderer.domElement);
      controls.enableDamping = false;
      controls.screenSpacePanning = false;
      // controls.minDistance = 2;
      // controls.maxDistance = 1000;
      controls.addEventListener("change", () => {
        renderer.render(scene, this.camera);
        console.log(this.camera);
      });
      // 渲染循环
      const animate = () => {
        requestAnimationFrame(animate);
        renderer.render(scene, this.camera);
        this.camera.updateProjectionMatrix();
      };
      animate();
    },
    updateCamera() {
      // 更新相机的视场角和位置
      this.camera.fov = this.fov;
      this.camera.position.set(
        this.cameraPosition.x,
        this.cameraPosition.y,
        this.cameraPosition.z
      );
      this.camera.updateProjectionMatrix(); // 更新投影矩阵
    },
  },
};
</script>

<style scoped>
#canvasContainer {
  width: 100%;
  height: 100vh;
}
</style>



