import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { FBXLoader } from "three/examples/jsm/loaders/FBXLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import {
  CSS3DRenderer,
  CSS3DObject,
} from "three/examples/jsm/renderers/CSS3DRenderer";
import {
  CSS2DRenderer,
  CSS2DObject,
} from "three/examples/jsm/renderers/CSS2DRenderer.js";

export default class RenderModel {
  constructor(
    canvasContainer,
    config = {
      camera: {
        fov: 45,
        near: 0.1,
        far: 3000,
        position: [4, 2, -5],
        rotation: [0, 0, 0],
      },
    }
  ) {
    this.canvasContainer = canvasContainer;
    this.camera; // 相机
    this.scene = null; // 场景
    this.renderer; //渲染器
    this.controls; // 控制器
    this.model; // 模型

    this.css3DRenderer = null; // 3d文字渲染器
    this.CSS2DRenderer = null; // 2d文字渲染器

    this.css3dControls = null; // 3d文字控制器
    this.css2dControls = null; // 2d文字控制器

    this.gridHelper; // 网格辅助线
    this.axesHelper; // 坐标轴辅助线
    this.init(config);
  }

  init(config) {
    this.initRenderer(); //初始化渲染器
    this.initScene(); //初始化场景
    this.initCamera(config.camera); //初始化相机
    this.initLight(); // 创建灯光
    this.initControl(); //初始化控制器，控制摄像头,控制器一定要在渲染器后
    // this.initSkybox();
    this.createGridHelper(); // 创建网格辅助线

    this.createAxesHelper(); // 创建坐标系
    window.addEventListener("resize", this.onWindowResize.bind(this), false);
  }

  initRenderer() {
    // 创建 WebGL 渲染器，设置抗锯齿、透明度和绘图缓冲区
    this.renderer = new THREE.WebGLRenderer({
      antialias: true, // 开启抗锯齿，以平滑边缘
      alpha: true, // 启用透明背景
      preserveDrawingBuffer: true, // 保持绘图缓冲区，允许使用 toDataURL 导出图像
    });
    this.renderer.setPixelRatio(window.devicePixelRatio); // 设置屏幕像素比以提高渲染质量
    const { clientHeight, clientWidth } = this.canvasContainer;
    this.renderer.setSize(clientWidth, clientHeight); // 获取容器的宽度和高度，并设置渲染器的尺寸
    this.renderer.toneMapping = THREE.ReinhardToneMapping; // 设置色调映射为 Reinhard 算法，这是一种有效的色调映射技术
    this.renderer.autoClear = true; // 自动清除颜色和深度缓冲区
    this.renderer.outputColorSpace = THREE.SRGBColorSpace; // 设置输出颜色空间为 sRGB，这对于正确的颜色渲染很重要
    this.renderer.toneMappingExposure = 2; // 设置曝光值，以控制渲染的亮度，值越高亮度越强
    this.renderer.shadowMap.enabled = true; // 启用阴影映射
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap; // 设置阴影映射类型为软阴影
    this.canvasContainer.appendChild(this.renderer.domElement); // 将渲染器的 DOM 元素添加到画布容器中

    // 创建一个CSS3DRenderer
    this.css3DRenderer = new CSS3DRenderer();
    this.css3DRenderer.setSize(clientWidth, clientHeight);
    this.css3DRenderer.domElement.style.position = "absolute";
    this.css3DRenderer.domElement.style.pointerEvents = "none";
    this.css3DRenderer.domElement.style.top = 0;
    this.canvasContainer.appendChild(this.css3DRenderer.domElement);

    // 创建一个CSS2DRenderer
    this.CSS2DRenderer = new CSS2DRenderer();
    this.CSS2DRenderer.setSize(clientWidth, clientHeight);
    this.CSS2DRenderer.domElement.style.position = "absolute";
    this.CSS2DRenderer.domElement.style.pointerEvents = "none";
    this.CSS2DRenderer.domElement.style.top = 0;
    this.CSS2DRenderer.domElement.style.left = 0;
    this.canvasContainer.appendChild(this.CSS2DRenderer.domElement);
  }

  // 创建场景
  async initScene() {
    this.scene = new THREE.Scene();
  }

  initLight() {
    // 创建环境光
    this.ambientLight = new THREE.AmbientLight("#fff", 0.8);
    this.ambientLight.visible = true;
    this.scene.add(this.ambientLight);

    // 创建平行光
    this.directionalLight = new THREE.DirectionalLight("#fff", 2); // 设置平行光的强度1-10
    this.directionalLight.position.set(-1.44, 2.2, 1);
    this.directionalLight.castShadow = true;
    this.directionalLight.visible = true;
    this.scene.add(this.directionalLight);

    // 创建平行光辅助线
    // this.directionalLightHelper = new THREE.DirectionalLightHelper(
    //   this.directionalLight,
    //   0.3
    // );
    // this.directionalLightHelper.visible = true;
    // this.scene.add(this.directionalLightHelper);

    // 创建点光源
    // this.pointLight = new THREE.PointLight('#fff', 5, 100);
    // this.pointLight.visible = true;
    // this.scene.add(this.pointLight);

    // 创建点光源辅助线
    // this.pointLightHelper = new THREE.PointLightHelper(this.pointLight, 0.5);
    // this.pointLightHelper.visible = true;
    // this.scene.add(this.pointLightHelper);

    //  创建聚光灯
    //  this.spotLight = new THREE.SpotLight("#00BABD", 900);
    //  this.spotLight.visible = true;
    //  this.spotLight.map = new THREE.TextureLoader().load("/static/skybox/daytime/negx.jpg");
    //  this.spotLight.map = new THREE.TextureLoader().load("/static/skybox/daytime/negx.jpg");
    //  this.spotLight.decay = 2;
    //  this.spotLight.shadow.mapSize.width = 1920;
    //  this.spotLight.shadow.mapSize.height = 1080;
    //  this.spotLight.shadow.camera.near = 1;
    //  this.spotLight.shadow.camera.far = 10;
    //  this.scene.add(this.spotLight);

    //创建聚光灯辅助线
    // this.spotLightHelper = new THREE.SpotLightHelper(this.spotLight);
    // this.spotLightHelper.visible = true;
    // this.scene.add(this.spotLightHelper);

    // const geometry = new THREE.PlaneGeometry(2000, 2000);
    // const planeMaterial = new THREE.ShadowMaterial({
    //   color: 'red',
    //   opacity: 0.5,
    // });
    // this.planeGeometry = new THREE.Mesh(geometry, planeMaterial);
    // this.planeGeometry.rotation.x = -Math.PI / 2;
    // this.planeGeometry.position.set(0, -1.2, 0);
    // this.planeGeometry.receiveShadow = true;
    // this.planeGeometry.material.side = THREE.DoubleSide;
    // this.planeGeometry.material.color.set("blue");
    // this.planeGeometry.geometry.verticesNeedUpdate = true;
    // this.scene.add(this.planeGeometry);
  }
  createAxesHelper() {
    // 创建坐标轴辅助线
    this.axesHelper = new THREE.AxesHelper(50); // 5 是坐标轴的长度，可以根据需要调整
    this.axesHelper.position.set(0, 0, 0); // 设置坐标轴位置，可以根据需要调整
    this.scene.add(this.axesHelper);
  }
  initCamera(configdata) {
    console.log("``````````````````", configdata);
    const { clientHeight, clientWidth } = this.canvasContainer;
    this.camera = new THREE.PerspectiveCamera(
      configdata.fov,
      clientWidth / clientHeight,
      configdata.near,
      configdata.far
    );
    // 设置默认位置（可以根据需求调整）
    this.camera.position.set(...configdata.position);
    this.camera.lookAt(0, 0, 0);
    this.camera.rotation.set(
      configdata.rotation[0] * (Math.PI / 180), // x 轴旋转
      configdata.rotation[1] * (Math.PI / 180), // y 轴旋转
      configdata.rotation[2] * (Math.PI / 180) // z 轴旋转
    );
  }
  //
  initControl() {
    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.controls.enablePan = false;
    this.controls.enableDamping = true;
    this.controls.target.set(0, 0, 0);
    this.controls.update();

    //3D 标签控制器
    this.css3dControls = new OrbitControls(
      this.camera,
      this.css3DRenderer.domElement
    );
    this.css3dControls.enablePan = false;
    this.css3dControls.enableDamping = true;
    this.css3dControls.target.set(0, 0, 0);
    this.css3dControls.update();

    //3D 标签控制器
    // this.css2dControls = new OrbitControls(
    //   this.camera,
    //   this.CSS2DRenderer.domElement
    // );
    // this.css2dControls.enablePan = false;
    // this.css2dControls.enableDamping = true;
    // this.css2dControls.target.set(0, 0, 0);
    // this.css2dControls.update();
  }
  //
  initSkybox() {
    const loader = new THREE.TextureLoader();
    const texture = loader.load("path/to/your/skybox.jpg"); // 替换为你的天空盒纹理路径
    const geometry = new THREE.BoxGeometry(100, 100, 100);
    const material = new THREE.MeshBasicMaterial({
      map: texture,
      side: THREE.BackSide,
    });
    const skybox = new THREE.Mesh(geometry, material);
    this.scene.add(skybox);
  }
  loadModel(url) {
    return new Promise((resolve, reject) => {
      const extension = url.split(".").pop().toLowerCase();
      let loader;

      if (extension === "gltf" || extension === "glb") {
        loader = new GLTFLoader();
      } else if (extension === "fbx") {
        loader = new FBXLoader();
      } else {
        reject(new Error("Unsupported file type"));
        return;
      }

      loader.load(
        url,
        (model) => {
          resolve(model);
        },
        undefined,
        (error) => {
          console.error(error);
          reject(error);
        }
      );
    });
  }

  // 创建3D标签
  tag3D(element, config) {
    const css3dRender = new CSS3DObject(element);
    css3dRender.scale.set(0.3,0.3,0.3); // 设置缩放
    return css3dRender; // 返回创建的 CSS3DObject
  }

  tag2D(div) {
    var label = new CSS2DObject(div);
    return label; //返回CSS2模型标签
  }
  // 场景动画渲染
  sceneAnimation() {
    requestAnimationFrame(this.sceneAnimation.bind(this));
    this.css3DRenderer.render(this.scene, this.camera);
    this.CSS2DRenderer.render(this.scene, this.camera);
    this.renderer.render(this.scene, this.camera);
    this.controls.update(); // 更新控制器
  }

  // 创建网格
  createGridHelper(size = 100, divisions = 100) {
    const gridHelper = new THREE.GridHelper(size, divisions); // 第一种颜色（主线）和第二种颜色（次线）
    // gridHelper.rotation.x = Math.PI / 2; // 如果需要，可以旋转网格
    gridHelper.material.opacity = 0.2; // 设置不透明度
    // gridHelper.material.transparent = true; // 设置透明

    this.scene.add(gridHelper);
  }

  // 点击事件处理器
  onClick(event, granaryArr) {
    // 获取画布的边界矩形
    const rect = this.canvasContainer.getBoundingClientRect();
    const offsetX = event.clientX - rect.left; // 鼠标相对画布的 X 坐标
    const offsetY = event.clientY - rect.top; // 鼠标相对画布的 Y 坐标

    const raycaster = new THREE.Raycaster(); // 创建射线投射器
    const mouse = new THREE.Vector2(); // 创建鼠标坐标向量

    // 将鼠标坐标标准化到[-1, 1]范围
    mouse.x = (offsetX / rect.width) * 2 - 1;
    mouse.y = -(offsetY / rect.height) * 2 + 1;

    // 从相机位置发射射线
    raycaster.setFromCamera(mouse, this.camera);

    var intersects = raycaster.intersectObjects(
      granaryArr ? granaryArr : this.scene.children
    );
    // console.log("射线器返回的对象", intersects);
    // console.log("射线投射器返回的对象 点point", intersects[0].point);
    // console.log("射线投射器的对象 几何体",intersects[0].object.geometry.vertices)
    // intersects.length大于0说明，说明选中了模型
    if (this.chooseMesh) {
      this.chooseMesh.material.color.set(0xffffff); // 把上次选中的mesh设置为原来的颜色
    }
    if (intersects.length > 0) {
      this.chooseMesh = intersects[0].object;
      this.chooseMesh.material.color.set(0x00ffff); //选中改变颜色，这样材质颜色贴图.map和color颜色会相乘
      this.scene.remove(this.highlightBox);
      this.highlightBox = new THREE.BoxHelper(
        this.chooseMesh,
        new THREE.Color(0x00ffff)
      ); // 使用第一个交点对象创建高亮框

      this.scene.add(this.highlightBox); // 将高亮框添加到场景中
    } else {
      this.scene.remove(this.highlightBox);
    }
  }

  // 窗口大小改变事件处理器
  onWindowResize() {
    const { clientHeight, clientWidth } = this.canvasContainer;
    this.camera.aspect = clientWidth / clientHeight;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(clientWidth, clientHeight);
  }
}



