<template>
  <el-tabs type="border-card" tab-position="left" v-model="activeName">
    <el-tab-pane label="box1" name="box1">
      <box1 v-if="activeName == 'box1'"></box1> </el-tab-pane
    ><el-tab-pane label="box2" name="box2">
      <box2 v-if="activeName == 'box2'"></box2>
    </el-tab-pane>
    <el-tab-pane label="box3" name="box3">
      <box3 v-if="activeName == 'box3'"></box3>
    </el-tab-pane>
    <el-tab-pane label="box4" name="box4">
      <box4 v-if="activeName == 'box4'"></box4>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
export default {
  components: {
    box1: () => import("./box1.vue"),
    box2: () => import("./box2.vue"),
    box3: () => import("./box3.vue"),
    box4: () => import("./box4.vue"),
  },
  data() {
    return {
      activeName: "box4",
    };
  },
};
</script>
<style scoped></style>



