<template>
  <div class="image-list" v-if="list && list.length">
    <ul
      class="drag-container"
      @dragstart="onDragStart"
      @dragover="onDragOver"
      @dragend="onDragEnd"
      ref="imgList"
    >
      <li
        v-for="(item, idx) in list"
        :key="item"
        class="drag-list"
        draggable="true"
      >
        <img :src="item" alt="" :index="idx" />
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "drag-image-list",
  props: {
    list: Array,
  },

  data() {
    return {
      draging: null,//被拖拽的对象
      target: null,
    }
  },
  methods: {
    onDragStart(event) {
      //   console.log("start");
      this.draging = event.target;
    },
    onDragOver(event) {
      // console.log('drag move')
      event.preventDefault();
      let target = event.target;
      if (target.nodeName === "IMG") {
        this.target = target
      }
    },
    onDragEnd(event) {
      this.$emit("draging", this.draging.getAttribute('index'), this.target.getAttribute('index'))
    },
  }
}
</script>

<style>
.drag-container li {
  float: left;
  margin-right: 10px;
}
.drag-container li img {
  width: 100px;
  height: 100px;
}
</style>


