<template>
  <div>
    <div
      ref="model"
      style="
        width: calc(100vw -600px);
        height: 75vh;
        margin: 30px;
        border: 1px solid pink;
        position: relative;
      "
    ></div>
  </div>
</template>

<script>
import renderModel from "./js/renderModel";
import * as THREE from "three";
import { createFlame } from "./js/flame";

export default {
  components: {},
  mounted() {
    this.init();
  },
  data() {
    return {};
  },
  watch: {},
  methods: {
    init() {
      let config = {
        camera: {
          fov: 45,
          near: 0.1,
          far: 3000,
          position: [4, 2, -5],
          rotation: [0, 0, 0],
        },
      };
      this.modelApi = new renderModel(this.$refs.model);

      this.modelApi
        .loadModel("/static/GLB/scene.gltf")
        .then((gltf) => {
          console.log("模型加载成功", gltf);

          this.modelApi.scene.add(gltf.scene); // 将模型组添加到场景中

          this.modelApi.sceneAnimation();
        })
        .catch((error) => {
          console.error("模型加载失败", error);
        });

      this.$refs.model.addEventListener("click", (event) => {
        this.modelApi.onClick(event);
      });
    },
  },
};
</script>

<style lang="scss" scopedF>
/* 粮仓标签的样式属性 */
.tag {
  box-shadow: 0 0 2px #00ffff inset;
  background: linear-gradient(#00ffff, #00ffff) left top,
    linear-gradient(#00ffff, #00ffff) left top,
    linear-gradient(#00ffff, #00ffff) right bottom,
    linear-gradient(#00ffff, #00ffff) right bottom;
  background-repeat: no-repeat;
  background-size: 1px 6px, 6px 1px;
  background-color: rgba(0, 0, 0, 0.4);
  color: #ffffff;
  font-size: 16px;
  padding: 4px 10px;
}
</style>



