<template lang="">
  <div>
    <highcharts :options="chartOptions"></highcharts>
  </div>
</template>
<script>
import { Chart } from 'highcharts-vue'
export default {
  components: {
    highcharts: Chart
  },
  data() {
    return {
      chartOptions: {
        series: [{
          data: [1, 2, 3] // sample data
        }]
      }
    }
  }
}
</script>
<style lang="">
</style>


