<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="用户管理" name="first">
      <echart1 />
    </el-tab-pane>
    <el-tab-pane label="配置管理" name="second">
      <echart2 />
    </el-tab-pane>
    <el-tab-pane label="角色管理" name="third"> <echart3 /></el-tab-pane>
    <el-tab-pane label="定时任务补偿" name="fourth"><echart4 v-if="activeName=='fourth'"/></el-tab-pane>
    <el-tab-pane label="1" name="1"><echart5 /></el-tab-pane>
  </el-tabs>
</template>
<script>

export default {
  components: {
    echart1: () => import('./echart1.vue'),
    echart2: () => import('./echart2.vue'),
    echart3: () => import('./echart3.vue'),
    echart4: () => import('./echart4.vue'),
    echart5: () => import('./echart5.vue'),
  },
  data() {
    return {
      activeName: 'fourth'
    };
  },
  methods: {
    handleClick(tab, event) {
      // console.log(tab, event);
    }
  }
};
</script>


