import * as THREE from "three";

class Skybox {
  constructor(scene, path, format = ".jpg") {
    this.scene = scene; // 场景引用
    this.path = path; // 纹理路径
    this.format = format; // 纹理格式
    this.cubeTexture = null; // 保存天空盒纹理
  }

  // 加载天空盒
  loadSkybox() {
    // 创建一个 THREE.CubeTextureLoader 实例 loaderbox 用于加载立方体贴图。
    const loaderbox = new THREE.CubeTextureLoader();
    // 使用 loaderbox.load 方法加载六张天空盒图片，分别代表天空盒的正面、背面、顶部、底部、左侧和右侧。
    const cubeTexture = loaderbox.load([
      this.path + "posx" + this.format, // 右
      this.path + "negx" + this.format, // 左
      this.path + "posy" + this.format, // 上
      this.path + "negy" + this.format, // 下
      this.path + "posz" + this.format, // 前
      this.path + "negz" + this.format, // 后

      
    ]);

    // 需要把色彩空间编码改一下
    // cubeTexture.encoding = THREE.sRGBEncoding;
    // 最后将设置好的立方体贴图赋值给场景对象的 background 属性，即将该立方体贴图作为场景的天空盒背景。
    this.scene.background = cubeTexture;
    this.scene.fog = new THREE.Fog(new THREE.Color(0xa0a0a0), 500, 2000);
  }
}

export default Skybox;



