<template>
    <div class="bodyRight">
        <div class="bodyRightLabel">
            <el-spin :spinning="isLoading">
                <!-- ***.下面是第四步 -->
                <draggable v-model="tagsList" class="bodyRightdraggable" @sort="onDraggableUpdate" animation="300"
                    chosenClass="chosen">
                    <div v-for="item in tagsList" :key="item.value">
                        <el-tag class="tabs" :key="item.value" closable @close="deleteTag(item)"
                            style="margin-bottom: 5px">
                            {{ item.label }}
                        </el-tag>
                    </div>
                </draggable>
            </el-spin>
        </div>
    </div>
</template>
<script>
//第一步 ***.引入组件
import draggable from 'vuedraggable'
export default {
    components: {
        draggable, //***.第二步 
    },
    data() {
        return {
            tagsList: [],
            treeClass: '',
            classList: [],
            checkedCategoryKeys: [],
            categoryTree: [],
            defaultProps: {
                children: 'subGroupList',
                title: 'groupName',
                key: 'groupCode',
            },
            eventSelectedNode: null,
            isLoading: false,
        }
    },
    created() {
        this.load_init()
    },
    methods: {
        deleteTag(item) {
            console.log("deleteTag--item", item)
        },
        //拖拽动作   ***.第三步 拖拽完成事件
        onDraggableUpdate(e) {
            this.isLoading = true
            setTimeout(() => {
                this.isLoading = false
                //老位置
                const oldIndex = e.oldIndex
                //新位置
                const newIndex = e.newIndex
                const newSort = this.tagsList[e.newIndex].sort
                this.tagsList[e.newIndex].sort = this.tagsList[e.oldIndex].sort
                this.tagsList[e.oldIndex].sort = newSort
                console.log(this.tagsList)
                this.$message.success('顺序变更成功!')
            }, 1000)
        },
        //初始化
        load_init() {
            for (let index = 0; index < 11; index++) {
                this.classList.push({
                    value: 'val' + index,
                    label: '树层级_' + index,
                })
                this.tagsList.push({
                    value: 'val' + index,
                    label: '树层级_' + index,
                    //是否编辑
                    isEditName: false,
                    //排序
                    sort: index,
                    //loading
                    isLoading: false,
                })
            }
        },
    },
}
</script>
<style lang="scss">
.bodyRight {
    width: 65%;
    height: 100%;

    .bodyRightTitle {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 10px;
        line-height: 18px;
    }

    .bodyRightLabel {
        width: 100%;
        height: 90%;
        overflow-y: auto;
        border: 2px solid #d9d9d9;
        border-radius: 4px;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-content: flex-start;

        .bodyRightdraggable {
            display: flex;
            flex-wrap: wrap;
            padding: 10px;
        }

        .tabs {
            width: 125px;
            height: 35px;
            border: 1px dashed #ccc;
            border-radius: 25px;
            font-size: 16px;
            align-items: center;
            display: flex;
            cursor: pointer;

            .tagname {
                width: 100%;
                display: flex;
                justify-content: space-between;
            }

            i {
                display: none;
            }

            &:hover {
                i {
                    font-size: 16px;
                    color: red;
                    display: inline-block;
                }
            }
        }

        //***.第五步 样式
        .chosen {
            .tabs {
                border: 1px solid #1890ff;
                background-color: #fff !important;
                color: #1890ff;
                cursor: move;
            }
        }
    }
}
</style>


