<template>
    <div class="bigMapBox">
        <el-input v-model="address" maxlength="50" show-word-limit style="width: 1226px" placeholder="请输入" />
        <div class="mapBox" id="mapBox"></div>
    </div>
</template>
<script>
import AMapLoader from "@amap/amap-jsapi-loader";

export default {
    data() {
        return {
            address: '',
        };
    },
    created() { },
    mounted() {
        this.initAMap();
    },
    methods: {
        initAMap() {
            window._AMapSecurityConfig = {
                securityJsCode: "4f85328b2e93e00e4a2991b8a056dbba",
            };
            AMapLoader.reset()
            AMapLoader.load({
                key: "4f85328b2e93e00e4a2991b8a056dbba", // 申请好的Web端开发者Key，首次调用 load 时必填
                version: "2.0", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
                plugins: ["AMap.Geocoder"], //需要使用的的插件列表，如比例尺'AMap.Scale'，支持添加多个如：['...','...']
            })
                .then((AMap) => {
                    this.map = new AMap.Map("mapBox", {
                        // 设置地图容器id
                        viewMode: "2D", // 是否为3D地图模式
                        zoom: 13, // 初始化地图级别
                        center: this.marker, // 初始化地图中心点位置
                    });
                    const icon = new AMap.Icon({
                        size: new AMap.Size(25, 34), //图标尺寸
                        image:
                            "//a.amap.com/jsapi_demos/static/demo-center/icons/poi-marker-red.png", //Icon 的图像
                        imageSize: new AMap.Size(25, 34),
                    });
                    this.marker = new AMap.Marker({
                        icon: icon,
                        position: this.marker,
                        offset: new AMap.Pixel(-13, -30),
                    });
                    this.marker.setMap(this.map);
                    this.map.on("click", (ev) => {

                        //触发事件的地理坐标，AMap.LngLat 类型
                        var lnglat = ev.lnglat;
                        this.marker.setPosition([ev.lnglat.lng, ev.lnglat.lat]);
                        AMap.plugin("AMap.Geocoder", () => {
                            var geocoder = new AMap.Geocoder({
                                city: "010", // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
                            });
                            var lnglats = [new AMap.LngLat(ev.lnglat.lng, ev.lnglat.lat)];
                            geocoder.getAddress(lnglats, (status, result) => {
                                if (status === "complete" && result.info === "OK") {
                                    console.log(result);
                                }
                            });
                        });

                    });
                })
                .catch((e) => {
                    console.log(e);
                });
        },
    },
};
</script>

<style scoped lang="scss">
.bigMapBox {
    max-width: 1353px;
    min-width: 500px;
    width: 100%;
    height: 180px;

    .mapBox {
        width: 1000px;
        height: 600px;

        .annotation {
            width: 21px;
            height: 28px;
            display: inline-block;
            background-size: 100% 100%;
            background-image: url("https://webapi.amap.com/theme/v1.3/markers/n/mark_b.png");
            background-repeat: no-repeat;
        }
    }

    .inputBox {
        max-width: 1353px;
        min-width: 500px;
        width: 100%;
        height: 52px;
    }
}

.BTNRigth {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    // align-items: center;
}
</style>


