<template lang="">
    <div>
      <div style="width:600px;height:400px;margin:30px auto 0" id="echarts3"></div>
    </div>
  </template>
<script>
import * as echarts from "echarts";
export default {
    data() {
        return {};
    },
    mounted() {
        var chartDom = document.getElementById("echarts3");
        var myChart = echarts.init(chartDom);
        var option;
        let xLabel = ['上海', '云南', '内蒙古', '北京', '台湾', '吉林', '四川', '天津', '宁夏', '西藏', '贵州', '辽宁', '重庆', '陕西', '香港', '黑龙江', '安徽', '山东', '山西', '广东', '广西', '新疆', '江苏', '江西', '河北', '河南', '浙江', '海南', '湖北', '湖南', '甘肃', '福建'];
        let goOutSchool = ['392', '229', '149', '269', '477', '477', '131', '460', '100', '170', '140', '152', '226', '138', '391', '181', '170', '137', '149', '193', '151', '161', '188', '142', '169', '142', '172', '356', '171', '150', '116', '161'];
        // var index = goOutSchool.findIndex((value, index) => value > 450);

        // console.log( (goOutSchool[index ]-goOutSchool[index- 1])/100, (goOutSchool[index] + goOutSchool[index - 1] / 450))

        option = {
            color: '#14b1eb',
            // backgroundColor: '#0e1c47',
            tooltip: {
                trigger: 'axis',
                backgroundColor: 'transparent',
                axisPointer: {
                    lineStyle: {
                        color: {
                            type: 'linear',
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [
                                {
                                    offset: 0,
                                    color: 'rgba(126,199,255,0)', // 0% 处的颜色
                                },
                                {
                                    offset: 0.5,
                                    color: 'rgba(126,199,255,1)', // 100% 处的颜色
                                },
                                {
                                    offset: 1,
                                    color: 'rgba(126,199,255,0)', // 100% 处的颜色
                                },
                            ],
                            global: false, // 缺省为 false
                        },
                    },
                },
            },
            legend: {
                // type: 'scroll',
                // icon: 'circle',
                // data: ['价格', '均价'],
                itemWidth: 25,  // 宽度
                itemHeight: 15, // 高度
                data: [
                    // { icon: 'circle', name: '价格' },
                    { icon: 'image://https://img.zcool.cn/community/01a346562cbb3432f87557015ecccf.jpg@1280w_1l_2o_100sh.jpg', name: '价格' },
                    { icon: 'rect', name: '平均价' },
                    // { icon: 'roundRect', name: '邮件营销' },
                    // { icon: 'triangle', name: '联盟广告' },
                    // { icon: 'diamond', name: '视频广告' },
                    // { icon: 'pin', name: 'SEO' },
                    // { icon: 'arrow', name: '定点投放' }
                ],
                right: 'center',
                top: '6%',
                itemWidth: 11,
                itemHeight: 11,
                textStyle: {
                    color: '#B3CFFF',
                    fontSize: 12,
                },
            },
            grid: {
                top: '18%',
                left: '15%',
                right: '5%',
                bottom: '25%',
                containLabel: true
            },
            xAxis: [
                {
                    type: 'category',
                    boundaryGap: false,
                    axisLine: {
                        //坐标轴轴线相关设置。数学上的x轴
                        show: true,
                        lineStyle: {
                            color: '#14b1eb',
                        },
                    },
                    axisLabel: {
                        //坐标轴刻度标签的相关设置
                        textStyle: {
                            color: '#14b1eb',
                            padding: 10,
                            fontSize: 14,
                        },
                        interval: 1,// 设置 y 轴刻度为 5 个
                        formatter: function (data) {
                            return data;
                        },
                    },
                    splitLine: {
                        show: true,
                        lineStyle: {
                            color: '#192a4411',
                        },
                    },
                    axisTick: {
                        show: false,
                    },
                    data: xLabel,
                },
            ],
            yAxis: [
                {
                    name: '均价',
                    nameTextStyle: {
                        color: '#14b1eb',
                        fontSize: 12,
                        padding: -5,
                    },
                    min: 0,
                    splitLine: {
                        show: true,
                        lineStyle: {
                            color: '#192a4411',
                        },
                    },
                    axisLine: {
                        show: true,
                        lineStyle: {
                            color: '#23365311',
                        },
                    },
                    axisLabel: {
                        show: true,
                        textStyle: {
                            color: '#14b1eb',
                            padding: 16,
                        },
                        formatter: function (value) {
                            if (value === 0) {
                                return value;
                            }
                            return value;
                        },
                    },
                    axisTick: {
                        show: false,
                    },
                },
            ],
            series: [
                {
                    name: '价格',
                    type: 'line',
                    smooth: true,
                    // showSymbol: false,
                    symbolSize: 8,
                    zlevel: 3,
                    lineStyle: {
                        normal: {
                            color: '#14b1eb',
                            shadowBlur: 3,
                            shadowColor: '#14b1eb66',
                            shadowOffsetY: 8,
                        },
                    },
                    markPoint: {
                        symbolSize: 54,
                        symbolOffset: [0, -23],// 设置markpoint图标的位置
                        symbol: 'image://https://img.zcool.cn/community/012b20586b7983a801219c776e28ff.jpg?x-oss-process=image/auto-orient,1/resize,m_lfit,w_1280,limit_1/sharpen,100/quality,q_100', // 图标的 URL
                        // symbol: 'circle',
                        label: {
                            normal: {
                                textStyle: {
                                    color: '#fff',


                                },
                            },
                        },
                        data: [
                            {
                                type: 'max',
                                name: '最大值',
                            },
                            {
                                type: 'min',
                                name: '最小值',
                            },
                            {
                                name: '平均值',
                                type: 'average',
                                formatter: function (params) {
                                    // 将数值转换为百分比单位
                                    return '平均值：' + (params.value).toFixed(2);
                                }
                            }
                        ],
                    },
                    areaStyle: {
                        normal: {
                            color: new echarts.graphic.LinearGradient(
                                0,
                                0,
                                0,
                                1,
                                [
                                    {
                                        offset: 0,
                                        color: '#14b1ebee',
                                    },
                                    {
                                        offset: 1,
                                        color: '#14b1eb00',
                                    },
                                ],
                                false
                            ),
                            shadowColor: '#14b1eb11',
                            shadowBlur: 10,
                        },
                    },
                    data: goOutSchool,
                },
                {
                    name: '平均价',
                    smooth: false,
                    type: 'line',
                    symbol: 'circle', // 折线点设定为实心点
                    symbolSize: 1, // 设定折线点的大小
                    data: new Array(goOutSchool.length).fill(450),
                    markPoint: {
                        data: [{
                            name: '交点',
                            coord: [1, 500]
                        }]
                    }

                }
            ],
        };

        option && myChart.setOption(option);
    },
    methods: {}
};
</script>
<style lang=""></style>


