<template>
  <div class="mapContainer">
    <div
      class="baseImage"
      ref="baseImage"
      :style="{ width: baseWidth + 'px', height: baseHeight + 'px', transform: `scale(${scale})` }"
      @wheel="onZoom"
      @mousedown="onDragStart"
    >
      <img
        :src="baseImgSrc"
        class="baseImg"
        :style="{ width: '100%', height: '100%' }"
        draggable="false"
        alt="Base Image"
      />
      <div
        v-for="icon in icons"
        :key="icon.id"
        class="icon"
        :style="{
          left: icon.x * scale + 'px',
          top: icon.y * scale + 'px',
        }"
        @click="onIconClick(icon)"
      >
        <img :src="icon.src" :style="{ width: iconSize * scale + 'px' }" alt="Icon" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      baseImgSrc: 'https://img2.baidu.com/it/u=1544882228,2394903552&fm=253&fmt=auto&app=138&f=JPEG?w=889&h=500',
      baseWidth: 1020, // 原始宽度
      baseHeight: 1000, // 原始高度
      scale: 1,
      icons: [],
      iconSize: 32,
      isDragging: false,
      startX: 0,
      startY: 0,
      startLeft: 0,
      startTop: 0,
    };
  },
  methods: {
    onZoom(event) {
      event.preventDefault();
      const zoomStep = 0.1;
      const scaleChange = event.deltaY < 0 ? zoomStep : -zoomStep;
      this.scale = Math.max(0.5, this.scale + scaleChange);
    },
    onDragStart(event) {
      this.isDragging = true;
      this.startX = event.clientX;
      this.startY = event.clientY;
      this.startLeft = this.$refs.baseImage.offsetLeft;
      this.startTop = this.$refs.baseImage.offsetTop;

      document.addEventListener('mousemove', this.onDragging);
      document.addEventListener('mouseup', this.onDragEnd);
    },
    onDragging(event) {
      if (!this.isDragging) return;
      const dx = event.clientX - this.startX;
      const dy = event.clientY - this.startY;
      this.$refs.baseImage.style.left = `${this.startLeft + dx}px`;
      this.$refs.baseImage.style.top = `${this.startTop + dy}px`;
    },
    onDragEnd() {
      this.isDragging = false;
      document.removeEventListener('mousemove', this.onDragging);
      document.removeEventListener('mouseup', this.onDragEnd);
    },
    onIconClick(icon) {
      alert(`Clicked on icon with ID: ${icon.id}`);
    },
  },
  mounted() {
    // 初始化图标数据
    const icons = [];
    for (let i = 0; i < 100; i++) {
      icons.push({
        id: 'icon-' + i,
        x: Math.random() * this.baseWidth,
        y: Math.random() * this.baseHeight,
        src: require('./小车汽车.png'), // 替换为你的图标路径
      });
    }

    icons.push({
        id: 'icon-123',
        x: 99,
        y: 99,
        src: require('./小车汽车.png'), // 替换为你的图标路径
      });
      icons.push({
        id: 'icon-123',
        x: 99,
        y: 91,
        src: require('./小车汽车.png'), // 替换为你的图标路径
      });
    this.icons = icons;
  },
};
</script>

<style scoped>
.mapContainer {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  border: 1px solid #ddd;
}

.baseImage {
  position: absolute;
  top: 0;
  left: 0;
  transform-origin: 0 0; /* 确保缩放以左上角为基准 */
}

.icon {
  position: absolute;
  transform: translate(-50%, -50%); /* 使图标居中显示 */
  cursor: pointer;
}

.baseImg {
  user-select: none;
}
</style>



