<template>
  <div class="new-page">
    <div ref="scheduler" class="gantt-container" />
  </div>
</template>
<script>
import scheduler from "dhtmlx-scheduler"; // 引入模块
import "dhtmlx-scheduler/codebase/dhtmlxscheduler_material.css";
export default {
  name: "DhtmlxGantt-Demo1",
  data() {
    return {
      events: [
        { id: 1, start_date: "2020-01-20 6:00", end_date: "2020-01-20 15:00", text: "Event 1" },
        { id: 2, start_date: "2020-01-23 6:00", end_date: "2020-01-23 20:00", text: "Event 2" }
      ]
    };
  },
  mounted() {
    scheduler.skin = "material";
    scheduler.config.header = [
      "day",
      "week",
      "month",
      "date",
      "prev",
      "today",
      "next"
    ];
    scheduler.init(this.$refs.scheduler, new Date(2020, 0, 20), "week");
    scheduler.parse(this.events);
    scheduler.attachEvent("onEventAdded", (id, ev) => {
      console.log("onEventAdded",id,ev)
    });
    scheduler.attachEvent("onEventChanged", (id, ev) => {
      console.log("onEventChanged",id,ev)
    });
    scheduler.attachEvent("onEventDeleted", (id, ev) => {
      console.log("onEventDeleted",id,ev)
    });
  }
};
</script>

<style scoped>
.gantt-container {
  height: 600px;
}
.new-page {
  height: 100%;
  background-color: #fff;
  padding: 20px;
  border-radius: 4px;
}
</style>



