<template>
  <div>
    <div ref="ganttYear" id="ganttYearDom" class="gantt-container" style="width: 100%; height: 450px"></div>
  </div>
</template>

<script>
import moment from 'moment' // moment格式化时间
import gantt from "dhtmlx-gantt"; // 引入模块
import "dhtmlx-gantt/codebase/dhtmlxgantt.css"; //皮肤
export default {
  name: 'resourcePlan',
  data() {
    return {

    }
  },
  created() {
    gantt.clearAll()
  },
  mounted() {
    this.init()
  },
  methods: {
    // moment,
    // 获取资源统计数据
    // getResourceInfo() {
    //   gantt.config.start_date = this.nowDate.format('YYYY-MM-DD')
    //   gantt.config.end_date = this.newDate.format('YYYY-MM-DD')
    // },
    // 初始化
    init() {
      console.log('初始化')

      gantt.i18n.setLocale('cn')//中文
      // gantt.config.autosize = false//自适应甘特图的尺寸大小, 使得在不出现滚动条的情况下, 显示全部任务
      // gantt.config.readonly = false// 只读模式：打开后不可以操作甘特图
      // gantt.config.date_format = '%Y-%m-%d'
      // gantt.config.task_date = '%Y-%m-%d'
      // gantt.config.keep_grid_width = false
      // gantt.config.grid_resize = true
      // gantt.config.initial_scroll = false
      // gantt.config.show_grid = true//是否显示左侧树表格
      // gantt.config.fit_tasks = true; //自适应
      // gantt.config.show_links = false//连线
      // gantt.config.scale_height = 80//表头高度      
      gantt.config.start_date = new Date(2024, 1, 1);
      gantt.config.end_date = new Date(2024, 12, 12);
      gantt.config.work_time = true //周末样式突出
      // gantt.config.details_on_dblclick = true//双击事件
      // gantt.config.show_errors = false//关闭所有错误提示信息：gantt有自己的异常消息，如果不关闭可能页面会弹出异常消息
      // gantt.config.drag_move = false//禁止拖动任务
      // gantt.config.drag_progress = false  //禁止拖动任务进度
      // gantt.config.drag_links = false//禁止拖放添加Link
      // gantt.config.min_column_width = 40
      //时间轴图表中，任务条形图的高度
      // gantt.config.task_height = 28
      //时间轴图表中，甘特图的高度
      // gantt.config.row_height = 36
      //时间轴图表中，如果不设置，只有行边框，区分上下的任务，设置之后带有列的边框，整个时间轴变成格子状。
      // gantt.config.show_task_cells = true
      //当task的长度改变时，自动调整图表坐标轴区间用于适配task的长度
      // gantt.config.fit_tasks = true
      // gantt.config.min_column_width = 50;
      // gantt.config.auto_types = true;
      // gantt.config.xml_date = "%Y-%m-%d";
      // gantt.config.start_on_monday = true;
      // gantt.config.autoscroll = true;
      // gantt.config.calendar_property = "start_date";
      // gantt.config.calendar_property = "end_date";




      // gantt.templates.timeline_cell_class = function (task, date) {
      //   if (!gantt.isWorkTime(date)) return 'week_end'
      //   return ''
      // }
      //表格列设置
      var textEditor = { type: "text", map_to: "userName" };
      var startDateEditor = { type: "date", map_to: "start_date" };
      var endDateEditor = { type: "end_date", map_to: "end_date" };
      // duration:1
      // end_date: Fri Feb 02 2024 00:00:00 GMT +0800(中国标准时间) { }
      // id:1713006115060
      // parent:0
      // progress:0
      // start_date: Thu Feb 01 2024 00:00:00 GMT +0800(中国标准时间) { }
      // text:"新任務"
      gantt.config.columns = [
        {
          //最左侧新增符号列，甘特图内置可选使用列
          name: 'add',
          label: '',
          width: '40',
        },
        {
          name: 'text',
          label: '名称',
          tree: true,
          width: '150',
          editor: { type: "text", map_to: "text" },
          resize: true,
          // template: function (obj) {
          //   // 通过 template 回调可以指定返回内容值
          //   return `
          //       <div class="gantt-span" style="width:130px;text-overflow:ellipsis;overflow: hidden;"> ${JSON.stringify(obj)}</div>
          //     `;
          // },
        },
        {
          name: 'start_date',
          label: '开始时间',
          editor: { type: "date", map_to: "start_date" },
          resize: true
          //hide: true,
        },
        {
          name: 'end_date',
          label: '结束时间',
          editor: { type: "date", map_to: "end_date" },
          resize: true
          //hide: true,
        },
        {
          name: 'progress',
          label: '进度',
          editor: { type: "text", map_to: "progress" },
          template: function (item) {
            return (item.progress * 100).toFixed(2) + '%'
          },
          //hide: true,
        },
        // {
        //   name: 'flag',
        //   label: '是否调配',
        //   //hide: true,
        // },
        // {
        //   name: 'reason',
        //   label: '原因',
        //   // template: function (item) {
        //   //   return byId(gantt.serverList('staff'), item.reason)
        //   // },
        //   //hide: true,
        // },
      ]
      // 弹框命名文字
      // gantt.locale.labels.section_priority = '不可调配原因1'
      // gantt.locale.labels.section_progorder = '项目名称2'
      gantt.locale.labels.section_text = '姓名'
      gantt.locale.labels.section_startDate = '开始时间'
      gantt.locale.labels.section_endDate = '结束时间'
      // gantt.locale.labels.section_deptName = '部室4'
      // gantt.locale.labels.section_extra = '部室4www'
      // gantt.locale.labels.section_other = '其他说明5'
      // gantt.locale.labels.message_ok = '确定'
      // gantt.locale.labels.confirm_deleting = '是否删除？'

      //配置Gantt内置弹出框元素(title内容)
      gantt.templates.lightbox_header = function (start_date, end_date, task) {
        // console.log('title内容', start_date, end_date, task)
        // return `<b>${task.userName}属性信息</b>`
        return `<b>${task.text}</b>`
      }
      let that = this
      gantt.serverList('staff',
        [{ key: '1', label: '编制报告', backgroundColor: '#BD3124', textColor: '#FFF' },
        { key: '2', label: '准备进场新项目', backgroundColor: '#BD3124', textColor: '#FFF' },
        { key: '3', label: '请假休整', backgroundColor: '#81B336', textColor: '#FFF' },
        { key: '4', label: '其他', backgroundColor: '#BD3124', textColor: '#FFF' }])

      // 添加弹窗属性
      gantt.config.lightbox.sections = [
        // { name: 'text', height: 30, map_to: 'text', type: 'select', options: gantt.serverList('staff') },
        // { name: 'text', height: 50, map_to: 'text', type: 'textarea', focus: true },
        // { name: "startDate", height: 50, map_to: "start_date", type: "date", focus: true, date_format: '%Y-%m-%d' },
        { name: "startDate", height: 50, map_to: "start_date", type: "date_editor", focus: true, date_format: '%Y-%m-%d' },
        { name: 'endDate', height: 50, map_to: 'end_date', type: 'date_editor', date_format: '%Y-%m-%d' },
        { name: 'text', height: 50, map_to: 'text', type: 'my_editor', date_format: '%Y-%m-%d' },
      ]

      gantt.form_blocks["my_editor"] = {
        render: function (sns) {
          return "<div class='dhx_cal_ltext' style='height:60px;'>" +
            "<input class='editor_description' type='text'>" +
            "</div>";
        },
        set_value: function (node, value, task) {
          // console.log('set_value', node, value, task)
          node.querySelector(".editor_description").value = value || "";
          // node.querySelector(".editor_holders").value = task.users || "";
        },
        get_value: function (node, task) {
          // console.log('get_value', node, task)
          // task.users = node.querySelector(".editor_holders").value;
          return node.querySelector(".editor_description").value;
        },
        focus: function (node) {
          var a = node.querySelector(".editor_description");
          a.select();
          a.focus();
        }
      };

      gantt.form_blocks["date_editor"] = {
        render: function (sns) {
          return "<div class='dhx_cal_ltext' style='height:60px;'>" +
            "<input class='date_editor' type='date'>" +
            "</div>";
        },
        set_value: function (node, value, task) {
          node.querySelector(".date_editor").value = moment(value).format('YYYY-MM-DD') || "";
        },
        get_value: function (node, task) {
          return new Date(node.querySelector(".date_editor").value);
        },
        focus: function (node) {
          var a = node.querySelector(".date_editor");
          a.select();
          a.focus();
        }
      };

      var weekScaleTemplate = function (date) {
        var dateToStr = gantt.date.date_to_str("%m %d");
        var endDate = gantt.date.add(gantt.date.add(date, 1, "week"), -1, "day");
        var weekNum = gantt.date.date_to_str("第 %W 周");
        return weekNum(date)
      };
      var daysStyle = function (date) {
        var dateToStr = gantt.date.date_to_str("%D");
        if (dateToStr(date) == "六" || dateToStr(date) == "日") return "weekend";
        return "";
      };
      // gantt.config.subscales = [
      gantt.config.scales = [

        // {
        //   unit: "month",
        //   step: 1,
        //   format: "%Y年%M",
        // }, 
        {
          unit: "week",
          step: 1,
          template: weekScaleTemplate,
        },
        {
          unit: "day",
          step: 1,
          format: "%d",
          // css: daysStyle
        }
      ];
      // gantt.config.scale_unit = "month";
      // gantt.config.step = 1;
      // gantt.config.date_scale = "%Y年%M";
      console.log(gantt)

      gantt.config.layout = {
        css: "gantt_container",
        cols: [{
          width: 480,
          min_width: 480,
          rows: [{
            view: "grid",
            scrollX: "gridScroll",
            scrollable: true,
            scrollY: "scrollVer"
          },
          {
            view: "scrollbar",
            id: "gridScroll",
            group: "horizontal"
          }
          ]
        },
        {
          resizer: true,
          width: 1
        },
        {
          rows: [{
            view: "timeline",
            scrollX: "scrollHor",
            scrollY: "scrollVer"
          },
          {
            view: "scrollbar",
            id: "scrollHor",
            group: "horizontal"
          }
          ]
        },
        {
          view: "scrollbar",
          id: "scrollVer"
        }
        ]
      };
      // 定义任务类名模板
      // gantt.templates.task_class = (start, end, task) => {
      //   if (task.custom_class) {
      //     return task.custom_class;
      //   }
      //   return "";
      // };

      // 自定义tooltip文本模板,鼠标移动显示信息
      // gantt.plugins({
      //   tooltip: true
      // });
      // // 自定义tooltip内容
      // gantt.templates.tooltip_text = function (start, end, task) {
      //   const t = gantt;
      //   const output = `<div style="font-size:14px;padding: 0 10px;width:250px;line-height:20px;white-space:normal;">
      //     <p style="font-weight:bold;margin: 8px 0;">自定义tooltip内容</p>
      //   </div>`;
      //   return output;
      // }

      // 初始化
      setTimeout(() => {
        gantt.clearAll()
        gantt.init(that.$refs.ganttYear)
        // gantt.parse([
        // 你的任务数据
        // { text: "Task 1", start_date: "2021-04-10", duration: 5, custom_class: "my-custom-class" },
        // ...更多任务
        // ]);
        document.querySelector('.gantt-container').style.height = '500px'
      }, 1000)
    },
    reload() {
      gantt.clearAll() // 从甘特图中删除所有任务和其他元素（包括标记）
      // gantt.init(this.$refs.ganttYear)
      // gantt.parse() // 数据解析
      gantt.render() // 呈现整个甘特图
    },
    getData() {

    },


  },
  computed: {

  },
  beforeDestroy() {
    // this.events.forEach((ele) => {
    //   gantt.detachEvent(ele)
    // })
  },
}
</script>

<style  scoped>

</style>


