// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router/permission";

import ElementUI from "element-ui";
import locale from "element-ui/lib/locale/lang/en";
import "element-ui/lib/theme-chalk/index.css";

Vue.use(ElementUI);
Vue.use(ElementUI, { locale });
Vue.config.productionTip = false;
import { i18n } from "@/lang";
Vue.use(ElementUI, {
  i18n: (key, value) => i18n.t(key, value), // 这里会根据选的语言切换Element-ui的语言
  size: "small", // set element-ui default size
});

// import Antd from 'ant-design-vue';
// import 'ant-design-vue/dist/antd.css';
// Vue.use(Antd);

import vueEsign from "vue-esign";
Vue.use(vueEsign);

Vue.prototype.$EventBus = new Vue();

import VueClipBoard from "vue-clipboard2";
Vue.use(VueClipBoard);

import BaiduMap from "vue-baidu-map";
Vue.use(BaiduMap, {
  // ak 是在百度地图开发者平台申请的密钥 详见 http://lbsyun.baidu.com/apiconsole/key */
  ak: "pQfSmaX5gY5iNhLjTdMBzEjf8VJdpkrI",
});

// 在main.js中注册:高德地图
import VueAMap from "vue-amap";
Vue.use(VueAMap);
VueAMap.initAMapApiLoader({
  key: "4f85328b2e93e00e4a2991b8a056dbba",
  plugin: [
    "AMap.AutoComplete", //输入提示插件
    "AMap.PlaceSearch", //POI搜索插件
    "AMap.Scale", //右下角缩略图插件 比例尺
    "AMap.OverView", //地图鹰眼插件
    "AMap.ToolBar", //地图工具条
    "AMap.MapType", //类别切换控件，实现默认图层与卫星图、实施交通图层之间切换的控制
    "AMap.PolyEditor", //编辑 折线多，边形
    "AMap.PolygonEditor", //编辑 折线多，边形
    "AMap.CircleEditor", //圆形编辑器插件
    "AMap.Geolocation", //定位控件，用来获取和展示用户主机所在的经纬度位置
  ],
  // 默认高德 sdk 版本为 1.4.4
  v: "2.0",
});

Vue.mixin({
  beforeCreate() {
    // ...逻辑
    // console.log("main.js mixin")
    // 这种方式会影响到每个组件的 beforeCreate 钩子函数
  },
});

import DragResizeDirective from "./directives/drag-resize";

Vue.directive("drag-resize", DragResizeDirective);


import DragDirective from './directives/drag';




import { scale } from "./directives/directive";
 
Vue.directive("scale", scale);

Vue.directive('drag', DragDirective);
/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  i18n,
  components: { App },
  template: "<App/>",
});



