<template>
  <div id="app">
    <div id="qrcode" ref="qrCodeUrl"></div>
    <button @click="down">下载</button>
  </div>
</template>

<script>
import { jsPDF } from "jspdf";
import QRCode from "qrcodejs2";

var Base64=require('js-base64').Base64
export default {
  name: "App",
  data() {
    return{a:''}
  },
  methods: {
    down() {
      const doc = new jsPDF("p", "pt"); // 获取实例
      // const res = doc.autoTableHtmlToJson(document.getElementById('indexTable'));
      // 设置中文字体

      doc.setFont("simhei");

      doc.autoTable({
        theme: "grid", // 主题
        startY: 80, // 距离上边的距离
        margin: 20, // 距离左右边的距离
        styles: {
          //设置表格的字体，不然表格中文也乱码
          fillColor: [255, 255, 255],
          font: "simhei",
          textColor: [0, 0, 0],
          halign: "left",
          fontSize: 12,
        },
        headStyles: { lineWidth: 1, halign: "center" },
        body: [], // 表格内容
      });

      const pageWidth = doc.getPageWidth(); // A4纸:595.28
      doc.setFontSize(12); // 设置文字大小,下一行起有效
      // doc.text(20, 60, `总分:${score}`);
      doc.save(`大数据指数.pdf`);
    },
  },

  created() {

  },
  mounted() {
    var a='{type:"table",name:"engineeringContactInfo",guid:"cfc5167748d64bf5923bd80e164cf9b6"}'
    this.a=Base64.encode(a)
    console.log(this.a)
    a=Base64.decode(this.a)
    console.log(a)
    var qrcode = new QRCode(this.$refs.qrCodeUrl, {
      text: this.a,
      width: 100,
      height: 100,
      colorDark: "#000000",
      colorLight: "#ffffff",
      correctLevel: QRCode.CorrectLevel.H,
    });
    let img = document.querySelector("canvas");
    this.dataURL = img.toDataURL();
  },
};
</script>

<style scoped>
#qrcode {
  width: 100px;
  height: 100px;
  background-color: red;
 
}
#qrcode::before{
 
}
</style>



