<template>
  <div>
    <div
      ref="model"
      style="
        width: calc(100vw -600px);
        height: 75vh;
        margin: 30px;
        border: 1px solid pink;
        position: relative;
      "
    ></div>
  </div>
</template>

<script>
import renderModel from "./js/renderModel";
import * as THREE from "three";
import { createFlame } from "./js/flame";
import SkyBoxs from "./js/SkyBoxs";
export default {
  components: {},
  mounted() {
    this.init();
  },
  data() {
    return {};
  },
  watch: {},
  methods: {
    init() {
      var modelGroup = new THREE.Group(); //声明一个组对象，用来添加加载成功的三维场景
      var granaryArr = []; //所有粮仓模型对象的集合，用于射线拾取使用
      let config = {
        camera: {
          fov: 45,
          near: 0.1,
          far: 3000,
          position: [229.152, 157.147, -5.929],
          rotation: [-91.98, 55.54, 92.62],
        },
      };
      this.modelApi = new renderModel(this.$refs.model, config);

      let Sky = new SkyBoxs(this.modelApi.scene);
      // Sky.addSkybox('daytime')
      Sky.addFog('red')
      // 使用加载模型的函数
      this.modelApi
        .loadModel("/static/GLB/modelZH.glb")
        .then((gltf) => {
          console.log("模型加载成功", gltf);

          const scaleFactor = 1;
          gltf.scene.scale.set(scaleFactor, scaleFactor, scaleFactor);

          // 遍历 gltf.scene 中的所有对象
          gltf.scene.traverse((object) => {
            // console.log('object对象', object.name);
            if (object.type === "Mesh") {
              // 批量更改所有 Mesh 的材质
              object.material = new THREE.MeshLambertMaterial({
                map: object.material.map, // 获取原来材质的颜色贴图属性值
                color: object.material.color, // 读取原来材质的颜色
                // color:'red'
              });
            }
          });

          var group = gltf.scene.getObjectByName("粮仓");
          // console.log('粮仓', group);
          let that = this;
          group.traverse(function (obj) {
            if (obj.type === "Mesh") {
              granaryArr.push(obj); //将粮仓对象添加到全局数组中

              // 创建div元素(作为标签)
              var div = document.createElement("div");
              div.innerHTML = obj.name;
              div.classList.add("tag");
              div.style.pointerEvents = "none"; //避免HTML标签遮挡三维场景的鼠标事件

              var label = that.modelApi.tag2D(div); //把粮仓名称obj.name作为标签
              var pos = new THREE.Vector3();
              obj.getWorldPosition(pos); //获取obj世界坐标、
              // 粮仓世界坐标对应粮仓底部圆心位置，如果标签像标注在粮仓底部，需要加上粮仓整体高度
              if (obj.parent.name == "立筒仓") {
                pos.y += 36; //加上粮仓顶部高度
              } else if (obj.parent.name == "浅圆仓") {
                pos.y += 20;
              } else if (obj.parent.name == "平房仓") {
                pos.y += 17;
              }

              label.position.copy(pos); //标签标注在obj世界坐标
              modelGroup.add(label); //标签插入model组对象中
              // console.log("标签位置:", label.position);
            }
          });

          var P_05Flame = this.granaryFlame(gltf, "P_05"); //平房仓 P_05
          modelGroup.add(P_05Flame); //火焰模型添加到model中
          modelGroup.add(gltf.scene);
          this.modelApi.scene.add(modelGroup); // 将模型组添加到场景中

          this.modelApi.sceneAnimation();
        })
        .catch((error) => {
          console.error("模型加载失败", error);
        });

      this.$refs.model.addEventListener("click", (event) => {
        this.modelApi.onClick(event, granaryArr);
      });
    },
    granaryFlame(gltf, name) {
      //name：粮仓名称编号
      var granary = gltf.scene.getObjectByName(name);
      var pos = new THREE.Vector3();
      granary.getWorldPosition(pos); //获取粮仓granary世界坐标设置火焰位置
      var flame = createFlame(); //创建一个对象
      flame.position.copy(pos);
      flame.scale.set(1, 1, 1);
      if (granary.parent.name == "立筒仓") {
        flame.position.y += 36; //加上粮仓顶部高度
      } else if (granary.parent.name == "浅圆仓") {
        flame.position.y += 20;
      } else if (granary.parent.name == "平房仓") {
        flame.position.y += 17;
      }
      flame.position.y += 4; //适当向下偏移

      // 火焰上方标签
      // 创建div元素(作为标签)
      var div = document.createElement("div");
      div.innerHTML = "粮仓 " + name + " 失火了 ！！！";
      div.classList.add("tag");
      div.style.pointerEvents = "none"; //避免HTML标签遮挡三维场景的鼠标事件

      var messageTag = this.modelApi.tag2D(div);
      flame.add(messageTag); //id"messageTag"对应的HTML元素作为three.js标签
      messageTag.position.y += 6; //考虑火焰高度 向上适当偏移
      flame.tag = messageTag; //火焰自定义一个属性指向自己的HTML标签
      return flame;
    },
  },
};
</script>

<style lang="scss" scopedF>
/* 粮仓标签的样式属性 */
.tag {
  box-shadow: 0 0 2px #00ffff inset;
  background: linear-gradient(#00ffff, #00ffff) left top,
    linear-gradient(#00ffff, #00ffff) left top,
    linear-gradient(#00ffff, #00ffff) right bottom,
    linear-gradient(#00ffff, #00ffff) right bottom;
  background-repeat: no-repeat;
  background-size: 1px 6px, 6px 1px;
  background-color: rgba(0, 0, 0, 0.4);
  color: #ffffff;
  font-size: 16px;
  padding: 4px 10px;
}
</style>



