<template>
    <div class="box">
      <!-- 地图组件 -->
      <el-amap
        vid="amapContainer"
        :zoom="zoom"
        :events="events"
        class="amapClass"
      ></el-amap>
    </div>
  </template>
  
  <script>
  // 引入地图加载实例方法
  import { lazyAMapApiLoaderInstance } from "vue-amap";
  export default {
    name: "gaode_ditu_amap",
    data() {
      
      return {
        map: null, // 地图实例存储变量
        zoom: 13, // 地图缩放比例。zoom值越小，越宏观、越大，越微观。大家可以更改zoom值看看效果
        events: {}, // 地图事件
        // 下方为上海虹桥火车站的经纬度坐标，我从网上搜索，大家可以更改这个坐标看看效果
        x: 121.320205,
        y: 31.193935,
      };
    },
    mounted() {
      // 插件加载成功以后去绘制地图
      lazyAMapApiLoaderInstance.load().then(() => {
        // 指定地图容器，并做相应的配置
        this.map = new AMap.Map("amapContainer", {
          center: new AMap.LngLat(this.x, this.y), // 地图中心点的经纬度位置配置
          zoom: this.zoom, // 地图缩放比例配置
        });
        // 标记的配置
        var marker = new AMap.Marker({
          map: this.map, // 使用此地图实例
          draggable: true,
          icon: "https://webapi.amap.com/theme/v1.3/markers/n/mark_b.png", // 标记定位的小图标
          position: [this.x, this.y], // 标记定位的小图标经纬度位置（一般标记定位的位置和地图中心点的位置要保持一致）
        });
        // 使用相关配置
        marker.setMap(this.map);
      });
    },
  };
  </script>
  <style scoped>
  .box {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 60px;
    background-color: #ced;
  }
  /* 指定地图的宽高 */
  .amapClass {
    width: 640px;
    height: 400px;
  }
  </style>


