<!-- 父组件 -->
<template>
  <div>
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="用户管理" name="first">
        <comDragStart :list="imageList" @draging="draging"></comDragStart>
      </el-tab-pane>
      <el-tab-pane label="配置管理" name="second">
        <vuedraggable></vuedraggable>
      </el-tab-pane>
      <el-tab-pane label="角色管理" name="third"
        ><boxdraggable></boxdraggable
      ></el-tab-pane>
      <el-tab-pane label="定时任务补偿" name="fourth">定时任务补偿</el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import comDragStart from "./comDragStart";
import vuedraggable from "./vuedraggable.vue";
import boxdraggable from "./boxdraggable.vue";

export default {
  components: {
    comDragStart,
    vuedraggable,
    boxdraggable
  },
  data() {
    return {
      activeName: "first",
      imageList: [
        `https://img2.baidu.com/it/u=1814268193,3619863984&fm=253&fmt=auto&app=138&f=JPEG?w=632&h=500&data=${Date.parse(
          new Date()
        )}`,
        "https://img1.baidu.com/it/u=1407750889,3441968730&fm=253&fmt=auto&app=120&f=JPEG?w=1200&h=799",
        "https://img0.baidu.com/it/u=1721391133,702358773&fm=253&fmt=auto&app=120&f=JPEG?w=500&h=625",
        "https://img1.baidu.com/it/u=3316754777,2519856621&fm=253&fmt=auto&app=138&f=JPEG?w=653&h=500",
        `https://img1.baidu.com/it/u=3851364429,4209170710&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500&data=${Date.parse(
          new Date()
        )}`,
        `https://img1.baidu.com/it/u=3851364429,4209170710&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500&data=${Date.parse(
          new Date()
        )}`,
        // "http://localhost:8080/assets/1.jpg",
      ],
    };
  },
  methods: {
    handleClick(tab, event) {
      // console.log(tab, event);
    },
    draging(a, b) {
      console.log("父组件排序", a, b);
    },
  },
};
</script>



