<template>
  <div>
    <div ref="chart" style="width: 500px; height: 400px;"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import { swoptions, findIntersection } from "./echartsoption"

export default {
  name: 'WaterFlowChart',
  data() {
    return {
      chart: null,
    };
  },
  mounted() {
    this.initChart();
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$refs.chart);
      let time = ["00:00", "01:00", "02:00", "03:00", "04:00", "05:00", "06:00", "07:00"]
      let sw = [5, 10, 13, 30, 78, 123, 20, 11, 11, 10, 20, 30]
      let fhgsw = 66
      let xxsw = 55
      let option = swoptions
      option.xAxis.data = time
      option.series[0].data = sw;
      option.series[1].markLine.data[0].yAxis = fhgsw;
      option.series[2].markLine.data[0].yAxis = xxsw;
      const fhgswcoord = findIntersection(
        sw,
        fhgsw
      )[0]

      const xxswcoord = findIntersection(
        sw,
        xxsw
      )[0]
      this.chart.setOption(option);
      setTimeout(() => {
        // 假设 chart 是 ECharts 实例，xAxisIndex 为 X 轴的索引（一般为 0）
        var xStartPixel = this.chart.convertToPixel({ xAxisIndex: 0, yAxisIndex: 0 }, [0, 0])[0];
        var xEndPixel = this.chart.convertToPixel({ xAxisIndex: 0, yAxisIndex: 0 }, [1, 0])[0];
        // 计算 X 轴每个刻度的像素宽度
        var xAxisPixelWidth = (xEndPixel - xStartPixel);
        console.log(fhgswcoord, xxswcoord,xAxisPixelWidth)
        option.series[1].markPoint = {
          symbol: "circle",
          symbolSize: 8,
          itemStyle: { color: "#FF5D5A" },
          data: [
            {
              coord: [Math.ceil(fhgswcoord[0]), fhgsw],
              symbolSize: 8,
              symbolOffset: [- xAxisPixelWidth * (1 - fhgswcoord[0] % 1), 0], // 计算相对偏移量
              // label: {
              //   position: "insideStartTop",
              //   fontSize: 12, //文字大小
              //   formatter: "防洪高水位", // 基线名称
              //   color: "#FF5D5A", //颜色
              // },
            },
          ],
        }
        option.series[2].markPoint = {
          symbol: "circle",
          symbolSize: 8,
          itemStyle: { color: "#FFEA0E" },
          data: [
            {
              coord: [Math.ceil(xxswcoord[0]), xxsw],
              symbolSize: 8,
              symbolOffset: [- xAxisPixelWidth * (1 - xxswcoord[0] % 1), 0], // 计算相对偏移量
              // label: {
              //   formatter: `达到洪限水位`,
              //   fontSize: 12, //文字大小
              //   color: "#FFEA0E", //颜色
              // },
            },
          ],
        }
        this.chart.setOption(option);
      }, 1000);
    },
  },
};
</script>

<style scoped></style>


