<template>
  <div class="bigmapBox1">
    <!-- <el-input v-model="address" maxlength="50" show-word-limit style="width: 1226px" placeholder="请输入" /> -->
    <div class="mapBox1" id="mapBox1"></div>
  </div>
</template>

<script>
import AMapLoader from "@amap/amap-jsapi-loader";

export default {
  data() {
    return {
      address: "",
      map: null,
      marker: null,
    };
  },
  created() {},
  mounted() {
    this.initAMap();
  },
  methods: {
    initAMap() {
      window._AMapSecurityConfig = {
        securityJsCode: "710d30fb3a9e65a245cdb60e6aa24051",
      };
      AMapLoader.reset();

      AMapLoader.load({
        key: "4f85328b2e93e00e4a2991b8a056dbba",
        version: "2.0",
        plugins: ["AMap.Geocoder", "Map3D", "AMap.Weather"],
        pitch: 40,
      })
        .then((AMap) => {
          var weather = new AMap.Weather();
          console.log("实时天气11", weather);
          //查询实时天气信息, 查询的城市到行政级别的城市，如朝阳区、杭州市
          weather.getLive("杭州市", function (err, data) {
            console.log("实时天气", err, data);
            if (!err) {
              console.log("天气", data);
            }
          });

          var imageLayer = new AMap.ImageLayer({
            url: "https://amappc.cn-hangzhou.oss-pub.aliyun-inc.com/lbs/static/img/dongwuyuan.jpg",
            bounds: new AMap.Bounds(
              [120.170468, 30.255933],
              [120.154086, 30.249539]
            ),
            zooms: [17, 20],
          });
          this.map = new AMap.Map("mapBox1", {
            // viewMode: "3D",
            // center: [118.150144, 24.5114],
            center: [120.162277, 30.252736],
            // center: [116.33719, 39.942384],
            // pitch: 55,
            rotateEnable: true,
            pitchEnable: true,
            // rotation: -15,
            rotateEnable: true,
            pitchEnable: true,
            zoom: 17,
            zooms: [12, 20],
            mapStyle: "amap://styles/02bce6e73b0670c007da7bdd6dfe17c6",
            layers: [AMap.createDefaultLayer(), imageLayer],
          });
          const bounds1 = this.map.getBounds(); // 获取当前视图的边界
          console.log("bounds", bounds1.northEast, bounds1.southWest);

          const bounds = new AMap.Bounds(
            [120.154086, 30.249539],
            [120.170468, 30.255933]
          );
          this.map.setBounds(bounds);

          var options = {
            areas: [
              {
                // 围栏1
                // visible: false, // 是否可见
                rejectTexture: true, // 是否屏蔽自定义地图的纹理
                path: [
                  [
                    [120.170468, 30.255933],
                    [120.154086, 30.255933],
                    [120.154086, 30.249539],
                    [120.170468, 30.249539],
                  ],
                ],
              },
            ],
          };

          // 外多边形坐标数组和内多边形坐标数组
          var outer = [
            new AMap.LngLat(-360, 90, true),
            new AMap.LngLat(-360, -90, true),
            new AMap.LngLat(360, -90, true),
            new AMap.LngLat(360, 90, true),
          ];
          var pathArray = [outer];
          pathArray.push.apply(pathArray, options.areas[0].path); // options.areas[0].path 外部区域 遮罩
          // pathArray = [ outer ] // 整个地图遮罩
          // pathArray = options.areas[0].path // options.areas[0].path 内部区域 遮罩

          new AMap.Polygon({
            path: pathArray,
            map: this.map,
            bubble: true,
            fillColor: "#fff", // 多边形填充颜色
            fillOpacity: 1, // 多边形填充透明度，默认为0.9
            strokeColor: "#00eeff", // 线条颜色
            strokeWeight: 2,
            strokeOpacity: 0.5, // 轮廓线透明度，默认为0.9
            strokeStyle: "dashed", // 轮廓线样式，实线:solid，虚线:dashed
            strokeDasharray: [10, 2, 10],
          });

          const createMarker = (position) => {
            const icon = new AMap.Icon({
              size: new AMap.Size(60, 50),
              image: require("./风扇.png"),
              imageSize: new AMap.Size(25, 34),
            });
            const marker = new AMap.Marker({
              icon: icon,
              position: position,
              //   offset: new AMap.Pixel(-13, -30),
            });
            //构建信息窗体中显示的内容
            var info = [];
            info.push(
              '<div class=\'input-card content-window-card\'><div><img style="float:left;width:67px;height:16px;" src=" https://webapi.amap.com/images/autonavi.png "/></div> '
            );
            let infoWindow = new AMap.InfoWindow({
              content: info.join(""), //使用默认信息窗体框样式，显示信息内容
            });

            marker.on("click", () => {
              infoWindow.open(this.map, [120.162277, 30.262736]);
            });
            return marker;
          };
          this.marker = createMarker([120.162277, 30.262736]);
          this.marker.setMap(this.map);

          this.map.on("click", (ev) => {
            console.log(ev.lnglat.lng, ev.lnglat.lat);
          });
        })
        .catch((error) => {
          console.error("地图加载失败：", error);
          // 可以在这里显示错误提示或者采取其他恢复措施
        });
    },
  },
};
</script>

<style scoped lang="scss">
.bigmapBox1 {
  width: 100%;
  height: 690px;

  .mapBox1 {
    width: 100%;
    height: 690px;

    .annotation {
      width: 21px;
      height: 28px;
      display: inline-block;
      background-size: 100% 100%;
      background-image: url("https://webapi.amap.com/theme/v1.3/markers/n/mark_b.png");
      background-repeat: no-repeat;
    }
  }

  .inputBox {
    width: 100%;
    height: 52px;
  }
}

.BTNRigth {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
</style>



