<template lang="">
    <div>
      <div style="width:600px;height:400px;margin:30px auto 0" id="main"></div>
    </div>
  </template>
  <script>
  import * as echarts from "echarts";
  export default {
    data() {
      return {};
    },
    mounted() {
      var chartDom = document.getElementById("main");
      var myChart = echarts.init(chartDom);
      var option;
  
      let datay = [10, 52, 2, 334, 390, 3200, 220]; // y轴
      let datax = ['T1', 'T2', 'T3', 'T4', 'T5', 'T6', 'T7']; // x轴
  
      let intervalArr = [0, 10, 50, 100, 300, 1000, 1200, 5000]
      // 新建一个数组绘制Echarts
      let datay2 = [];
      datay.forEach((item) => {
        // 寻找在 intervalArr 数据间隔里小于item的最大值
        let min_value = Math.max(...intervalArr.filter(v => v <= item));
        // 寻找 min_value 所在的下标
        let min_index = intervalArr.findIndex(v => v === min_value);
        // 计算新的数组 datay2 公式
        const datay2_value = (item - min_value) / (intervalArr[min_index + 1] - intervalArr[min_index]) + min_index;
        datay2.push(datay2_value);
      })
  
      console.log(`datay2`, datay2)
      option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          },
          formatter: params => {
            let d = `${params[0].name}<br>`;
            let item = [];
            params.forEach(el => {
              item.push(`${el.marker}${el.seriesName}：${datay[params[0].dataIndex]}<br>`);
            });
            return d + item.join('');
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: datax,
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            min: 0, //最小值是零
            // max: datay.length, // interval是datay最大值整数部分长度  = 8
            interval: 1, // 每条刻度线之间的距离设置为1，这样就有interval条刻度线
            axisLabel: {
              // 改变Y轴刻度名称，这里只改变了字段，Y轴的真实值还是没有改变的
              formatter: (v, i) => {
                return intervalArr[i]
                // if (i === 0) {
                //   return 0 + '';
                // } else {
                //   return Math.pow(10, i) + ''; // ''里面可以加单位
                // }
              }
            }
          }
        ],
        series: [
          {
            name: 'Direct',
            type: 'bar',
            barWidth: '60%',
            data: datay2
          }
        ]
      };
      option && myChart.setOption(option);
    },
    methods: {}
  };
  </script>
  <style lang=""></style>
  


