// 引入 Three.js 库
import * as THREE from "three";

// 创建一个火焰动画的函数
function createFlame() {
    // 定义火焰的宽度和高度
    var w = 16; // 火焰宽度
    var h = 1.6 * w; // 火焰高度
    // 创建一个平面几何体，作为火焰的基础形状
    var geometry = new THREE.PlaneBufferGeometry(w, h);
    // 调整几何体的位置，使火焰底部中点与局部坐标系原点重合
    geometry.translate(0, h / 2, 0); 

    // 创建纹理加载器
    var textureLoader = new THREE.TextureLoader();
    // 加载火焰的纹理图片
    var texture = textureLoader.load('/static/img/火焰.png'); 
    var num = 15; // 火焰动画的帧数
    // 设置纹理在 U 方向的重复数量，用于从纹理图中截取不同的帧
    texture.repeat.set(1 / num, 1); 

    // 创建火焰的材质
    var material = new THREE.MeshBasicMaterial({
        map: texture, // 使用加载的纹理
        transparent: true, // 允许透明度
        opacity: 0.4, // 设置整体透明度
        side: THREE.DoubleSide, // 双面可见
        depthWrite: false, // 不对深度缓冲区有任何影响
    });

    // 使用几何体和材质创建火焰网格模型
    var mesh = new THREE.Mesh(geometry, material); 
    // 创建一个组对象，用于管理多个火焰网格
    var flame = new THREE.Group(); 
    // 将多个火焰网格模型添加到组中，使用不同的旋转角度交叉叠加
    flame.add(mesh, 
              mesh.clone().rotateY(Math.PI / 2), 
              mesh.clone().rotateY(Math.PI / 4), 
              mesh.clone().rotateY(Math.PI / 4 * 3));

    // 初始化帧数和停止动画的变量
    var t = 0;
    var stopAnimationFrame = null;

    // 定义火焰动画循环的函数
    function UpdateLoop() {
        t += 0.1; // 调节火焰切换速度
        if (t > num) t = 0; // 如果帧数超出范围，则重置
        // 动态更新纹理偏移，播放火焰的关键帧动画
        texture.offset.x = Math.floor(t) / num; 
        // 请求下一帧动画
        stopAnimationFrame = window.requestAnimationFrame(UpdateLoop); 
    }
    // 开始动画循环
    UpdateLoop();

    // 定义停止动画的函数
    flame.stop = function () {
        window.cancelAnimationFrame(stopAnimationFrame); // 取消动画
    }

    // 返回创建的火焰组对象
    return flame;
}

// 导出 createFlame 函数以便在其他模块中使用
export { createFlame };



